@font-face
    font-family: "icons_11"
    src: url('../fonts/icons_11.eot')
    src: url('../fonts/icons_11.eot?#iefix') format('eot'), url('../fonts/icons_11.woff') format('woff'),url('../fonts/icons_11.woff2') format('woff2'), url('../fonts/icons_11.ttf') format('truetype'), url('../fonts/icons_11.svg#icons_11') format('svg')


@mixin icon-styles
    font-family: "icons_11"
    -webkit-font-smoothing: antialiased
    -moz-osx-font-smoothing: grayscale
    font-style: normal
    font-variant: normal
    font-weight: normal
    // speak: none; // only necessary if not using the private unicode range (firstGlyph option)
    text-decoration: none
    text-transform: none

    display: inline-block
    text-decoration: inherit
    // width: 1em
    line-height: 1em


%icon
    +icon-styles

