.desctop-view
  display: flex
  align-items: center
  +r(767)
    flex-direction: column
  &__box
    display: block
    position: relative
    width: 270px
    height: 150px
    background: #FFFFFF
    box-shadow: 6px 8px 30px 3px #E1E3EA
    border-radius: 20px
    margin-right: 30px
    +r(767)
      margin-right: 0
      margin-bottom: 30px
    +r(479)
      width: 100%
      margin-top: 10px
    span
      display: flex
      justify-content: center
      align-items: center
      position: absolute
      top: 0
      background-color: #EFF4FF
      border: 3px solid #A8C1FF
      box-sizing: border-box
      background-repeat: no-repeat
      background-position: center

    &.left span
      left: 0
      width: 70px
      height: 150px
      border-radius: 20px 0px 0px 20px
      background-image: url(../img/bg-filter-1.svg)

    &.right span
      right: 0
      width: 70px
      height: 150px
      border-radius: 0 20px 20px 0
      background-image: url(../img/bg-filter-1.svg)

    &.top span
      left: 0
      width: 100%
      height: 70px
      border-radius: 20px 20px 0 0
      background-image: url(../img/bg-filter-2.svg)

  &__list
    h6
      font-style: normal
      font-weight: 500
      font-size: 14px
      line-height: 17px
      letter-spacing: 0.02em
      color: #878C98
      margin-bottom: 15px
    ul
      list-style-type: none
      padding: 0
      margin: 0


.mobile-view
  display: flex
  align-items: center
  margin-left: 10px
  &__box
    display: block
    position: relative
    width: 120px
    height: 220px
    background: #FFFFFF
    box-shadow: 6px 8px 30px 3px #E1E3EA
    border-radius: 20px
    margin-right: 30px

    &:before
      content: ''
      display: block
      position: absolute
      top: 15px
      left: 50%
      transform: translateX(-50%)
      width: 22px
      height: 3px
      background: #E1E3EA
      border-radius: 3px

    &:after
      content: ''
      display: block
      position: absolute
      bottom: 15px
      left: 50%
      transform: translateX(-50%)
      width: 18px
      height: 8px
      background: #E1E3EA
      border-radius: 5px

    span
      display: flex
      justify-content: center
      align-items: center
      width: 70px
      height: 150px
      position: absolute
      top: 50%
      transform: translateY(-50%)
      background-color: #EFF4FF
      border: 3px solid #A8C1FF
      border-radius: 20px
      box-sizing: border-box
      background-repeat: no-repeat
      background-position: center
      background-image: url(../img/bg-filter-1.svg)

    &.left span
      left: -10px

    &.right span
      right: -10px

  &__list
    h6
      font-style: normal
      font-weight: 500
      font-size: 14px
      line-height: 17px
      letter-spacing: 0.02em
      color: #878C98
      margin-bottom: 15px
    ul
      list-style-type: none
      padding: 0
      margin: 0

table.filter-base-attr-table
  th,
  td
    padding: 10px 15px

    // &:nth-child(4),
    // &:nth-child(5)
    //   padding-left: 0
    //   &::before
    //     display: none

  .list-clear
    justify-content: space-between
    li
      & + li
        margin-top: 5px

      .check__box
        align-items: flex-start
        &::before
          margin-top: -3px
        &:after
          top: -3px
          transform: none

.filter-base-others-table
  th,
  td
    padding: 10px 15px

.table-before-btn.--filter-base-attr
  & > *
    right: 5px
    +r(767)
      right: 10px