.menu-list
  &__row
    flex-wrap: wrap
    width: 100%
    display: flex
    padding-left: 30px

    +r(767)
      padding-left: 15px

  &__item
    flex-wrap: wrap
    width: 100%
    display: flex
    position: relative

    &.is-opened
      &::before
        content: ''
        display: block
        width: 100%
        height: 100%
        border: 2px solid #D2D6DF
        pointer-events: none
        position: absolute
        z-index: 5

  &__col
    flex: 0 0 95px
    position: relative
    display: flex
    align-items: center
    justify-content: center
    padding: 15px 0
    font-weight: 500
    font-size: 12px
    line-height: 120%
    color: #30393E
    +r(767)
      flex: 0 0 55px

    &:before
      position: absolute
      left: 0
      width: 1px
      content: ''
      display: block
      top: 10px
      height: calc( 100% - 20px )
      background: #F0F1F4

    &::after
      position: absolute
      right: 0
      width: 100%
      content: ''
      display: block
      bottom: 0
      height: 1px
      background: #F0F1F4

    &:first-child
      flex: 1 1 auto
      justify-content: start
      max-width: calc( 100% - 95px )
      cursor: pointer

      +r(767)
        max-width: calc( 100% - 55px )

      &:before
        display: none

  &__name
    text-overflow: ellipsis
    white-space: nowrap
    overflow: hidden


.menu-sub-list
  flex: 0 0 100%
  padding-left: 30px
  display: none
  min-width: 0

  .menu-sub-list
    padding-left: 30px
    .menu-list__col
      font-weight: 400

  +r(767)
    padding-left: 15px
    .menu-sub-list
      padding-left: 15px

.menu-list-head
  .menu-list__col
    color: #B4BACA
    flex: 0 0 100%
    max-width: 100%!important
    padding-right: 95px