.day-status
  width: 30px
  height: 40px
  border-radius: 4px
  display: inline-block

  &.--green
    background: #A6F1BF

  &.--yellow
    background: #FDEB95

  &.--red
    background: #FFC4CB


.person
  display: flex
  align-items: center
  width: 310px
  padding-right: 40px
  padding-left: 15px
  cursor: pointer
  +r(1799)
    padding-left: 5px
  +r(568)
    width: 180px
    padding-right: 5px
  &__name
    white-space: nowrap
    text-overflow: ellipsis
    overflow: hidden
  &__color 
    width: 14px
    height: 14px
    flex: 0 0 14px
    border-radius: 2px
    display: block
    margin-right: 8px
  &__img
    width: 24px
    height: 24px
    flex: 0 0 24px
    margin-right: 8px
    img
      display: block
      max-width: 100%

.manager-table 
  &__date
    font-weight: 500
    font-size: 12px
    line-height: 100%
    color: #878C98
    margin-bottom: 4px
  &__day
    font-weight: 400
    font-size: 10px
    line-height: 100%
    letter-spacing: 0.02em
    color: #C3C8D5

  .td-weekend
    .day-status
      opacity: .6

:root 
  --manager-left: 0
  --manager-opacity: 0

table.manager-table
  position: relative
  +rmin(1800)
    &::before 
      width: 30px
      z-index: 10
      left: var(--manager-left)
      margin-left: -15px
      top: 0
      height: 100%
      content: ''
      position: absolute
      border-radius: 6px
      border: 2px solid #878C98
      pointer-events: none
      opacity: var(--manager-opacity)
  &.footable tbody td
    padding: 1px 0 0
    height: auto
    text-align: center
    height: 44px
    line-height: 1
    &::before
      display: none
    &.disabled
      .day-status
        background: #E1E3EA
  &.footable thead tr th
    padding: 4px 0 13px 0
    text-align: center
    height: 45px
    +rmin(992)
      border-bottom: 1px solid #E1E3EA
    &:first-child
      border-bottom: 1px solid transparent
  &.footable tbody tr:not(.footable-detail-row)
    border-bottom: 0
    td:first-child
      border-top-left-radius: 6px
      border-bottom-left-radius: 6px
    td:last-child
      border-top-right-radius: 6px
      border-bottom-right-radius: 6px

    +h
      td
        background: #F8F9FA
  &.footable tbody tr
    transition: 0.3s background ease
  &.footable tbody
    border-top: 0
  table.footable-details > tbody > tr > th:first-child
    height: 40px
    width: 20%
    +rmin(992)
      width: 310px
  +r(1199)
    &.footable tbody td:first-child
      padding: 2px 0 0
  table.footable-details > tbody > tr
    th:first-child
      padding-left: 31px!important
    td, th
      transition: 0.3s background ease
      text-align: left
    +h
      td, th
        background: #F8F9FA

  .footable-details
    margin-bottom: 10px

.manager-table-search
  position: relative

  .input-group
    position: absolute
    z-index: 10
    width: 270px
    +r(991)
      width: 100%
  .sr-only
    display: none
  .dropdown-toggle
    display: none
  .btn-primary
    position: absolute
    right: 0
    background: #fff
    color: #C3C8D5
    border: 0
    top: 50%
    transform: translateY(-50%)
    display: flex
    align-items: center
    justify-content: center
    z-index: 5
    .fooicon-search
      +icon(search)
  .form-control
    border-radius: 6px!important 

.days-count
  background: #F8F9FA
  border-radius: 4px
  font-size: 9px
  line-height: 100%
  color: #C3C8D5
  height: 40px
  display: flex
  align-items: center
  justify-content: center
  padding: 0 8px
  margin-top: -6px

  .fw-500
    font-size: 12px

.filter-btns 
  display: flex
  flex-wrap: wrap
  margin-bottom: 24px

  .filter-btn
    margin-right: 5px
    margin-bottom: 5px

.filter-btn
  background: #F0F1F4
  border-radius: 6px
  font-weight: 500
  font-size: 14px
  line-height: 100%
  color: #878C98
  padding: 10px
  border: 0
  +tr(.25s)

  +h
    background: #ffd50c
    color: #30393E

  
  &.active, &.is-active
    background: #FFDE3F
    color: #30393E


.board-list-wrap
  padding-bottom: 12px
  border-bottom: 1px solid #F0F1F4
  margin-bottom: 24px
.board-list
  display: flex
  flex-wrap: wrap
  margin: 0 -12px
  &__title
    font-weight: 500
    font-size: 14px
    line-height: 100%
    color: #5A5D65
    margin-bottom: 20px
  &__item
    padding: 0 12px
    margin-bottom: 12px
    display: none

    &.is-show
      display: block
      animation: selected 300ms 1 ease-in-out
  
  &__title-amout
    margin-left: 15px


@keyframes selected
  0%
    transform: translate(0, 25px)
    opacity: 0


.board
  background: #F0F1F4
  border-radius: 20px
  width: 100px
  height: 100px
  padding: 13px
  position: relative
  display: block

  &::before
    content:''
    display: block
    width: 16px
    height: 16px
    top: 0
    left: 0
    position: absolute
    left: 0
    top: 0
    opacity: 0
    border-radius: 50%

  &__decore
    background: #B4BACA
    filter: blur(10px)
    border-radius: 20px
    width: 70px
    height: 70px
    position: absolute
    bottom: 0
    left: 50%
    transform: translateX(-50%)
    z-index: -1
      

  &.board--yellow
    background: #FDEFAA
    .board__decore
      background: #C1AB3E
  &.board--green
    background: #D3F1DA
    .board__decore
      background: #4FAA6E
  &.board--red
    background: #FFDBE0
    .board__decore
      background: #D95464
  &.--marker-red
    &::before
      background: #FF5F72
      opacity: 1
  &.--marker-gray
    &::before
      background: #B4BACA
      opacity: 1

  &__number
    font-weight: 500
    font-size: 22px
    line-height: 100%
    letter-spacing: 0.02em
    color: #30393E
    margin-bottom: 13px
  &__people
    font-weight: 500
    font-size: 16px
    line-height: 100%
    color: #5A5D65
    margin-bottom: 13px
    &:before 
      font-size: 16px
      color: #30393E
      opacity: 0.3
  &__info
    font-weight: 400
    font-size: 12px
    line-height: 100%
    letter-spacing: 0.02em
    color: #5A5D65
  &__count
    width: 31px
    height: 31px
    font-weight: 500
    font-size: 16px
    line-height: 100%
    display: flex
    justify-content: center
    align-items: center
    text-align: center
    color: #878C98
    border-radius: 50%
    position: absolute
    right: 0
    top: 0
    z-index: 2
    &::before
      background: #78C592
      filter: blur(4px)
      content:''
      display: block
      width: 31px
      height: 31px
      top: 4px
      right: 4px
      z-index: -2
      border-radius: 50%
      position: absolute
    &::after
      width: 31px
      height: 31px
      content:''
      display: block
      border-radius: 50%
      position: absolute
      background: #FFFFFF
      border: 2px solid #BCEAC6
      left: 0
      top: 0
      z-index: -1
  &__dish
    background: #B4BACA
    position: absolute
    right: 0
    bottom: 0
    display: block
    width: 30px
    height: 30px
    display: flex 
    align-items: center
    justify-content: center
    color: #fff
    border-radius: 50%
    font-size: 26px

.borad-group
  flex-wrap: wrap
  display: none

  &.is-show
    display: flex
    animation: selected 300ms 1 ease-in-out
  .board-list__item
    display: block
  .board
    &::after
      height: 70px
      position: absolute
      left: -12px
      width: calc( 100% + 24px )
      content: ''
      top: 50%
      transform: translateY(-50%)
      background: #FFECEE
      display: block
      z-index: -1
      pointer-events: none
  .board-list__item:first-child
    .board
      &::after
        left: 0
        right: -12px
        width: calc( 100% + 12px )
  .board-list__item:last-child
    .board
      &::after
        width: calc( 100% + 12px )

.page-board-head
  display: flex
  align-items: center
  +r(767)
    flex-direction: column
    align-items: flex-start

    > *
      margin-bottom: 20px

  h4
    margin: 0 30px 0 0 

.board-head
