    +r(767)
      flex: 0 0 100%
  &__right
    flex: 0 0 calc(100% - 314px)
    padding-left: 43px
    min-width: 0
    display: flex
    flex-wrap: wrap
    align-items: flex-start
    +r(1650)
      flex: 0 0 calc(100% - 271px)
      padding-left: 20px
    +r(1580)
      flex-direction: column
    +r(767)
      flex: 0 0 100%

.employees-list
  padding-right: 43px 
  border-right: 1px solid #E1E3EA
  +r(1650)
