.table-before-btn.--small.--category > *
  right: 30px
  +r(1199)
    right: 10px

.custome-table
  display: block
  position: relative

.custome-table__head
  display: block
  position: relative

  .custome-table__row
    border-bottom: 1px solid #F0F1F4

    .custome-table__cell
      height: 55px

  .custome-table__cell
    &:first-child
      .title
        margin-left: 70px
        +r(1199)
          margin-left: 50px
        +r(767)
          margin-left: 0
      .check
        +r(767)
          display: none

    &:nth-child(2)
      -webkit-box-flex: 0
      -ms-flex: 0 0 310px
      flex: 0 0 310px
      padding-left: 30px !important
      +r(1199)
        padding-left: 10px!important

    &:nth-child(3)
      -webkit-box-flex: 0
      -ms-flex: 0 0 140px
      flex: 0 0 140px

    &:nth-child(4)
      -webkit-box-flex: 0
      -ms-flex: 0 0 95px
      flex: 0 0 95px

  .custome-table__cell-group
    +r(767)
      display: none

.custome-table__row
  display: grid
  grid-template-columns: 50% 50%
  position: relative
  +r(1199)
    grid-template-columns: 40% 60%
  +r(767)
    display: block

.custome-table__cell-group
  display: grid
  grid-template-columns: calc(100% - 235px) 140px 95px
  position: relative
  +r(1199)
    grid-template-columns: calc(100% - 155px) 100px 55px
  +r(767)
    display: flex
    flex-direction: column
    align-items: flex-start

  .custome-table__cell
    border-left: 1px solid #F0F1F4

    &:nth-child(1)
      max-width: 100% !important
      padding-left: 30px !important
      +r(1199)
        padding-left: 10px!important

.custome-table__cell
  box-sizing: border-box
  display: flex
  position: relative
  align-items: center
  padding: 10px 30px
  +r(1199)
    padding: 10px

  .d-flex
    min-width: 0

  &:nth-child(1)
    flex-grow: 1

  .check__box::before
    margin-right: 0

  .toggle-dropdown
    display: flex
    flex-grow: 1
    align-items: center
    justify-content: space-between
    cursor: pointer

    .value
      display: block
      max-width: 420px
      margin-right: auto

  .toggle-dropdown-btn
    -webkit-box-pack: center
    -ms-flex-pack: center
    -webkit-box-align: center
    -ms-flex-align: center
    -webkit-box-flex: 0
    -ms-flex: 0 0 35px
    display: -webkit-box
    display: -ms-flexbox
    display: flex
    flex: 0 0 35px
    align-items: center
    justify-content: center
    width: 35px
    height: 35px
    margin-left: 10px
    border: none
    border-radius: 6px
    background: #F8F9FA
    color: #B4BACA
    opacity: 0

    +r(767)
      opacity: 1

    +icon(arrow-down)

  .flag
    -webkit-box-flex: 0
    -ms-flex: 0 0 25px
    flex: 0 0 25px
    width: 25px
    height: 25px
    margin-right: 15px
    margin-left: 30px
    padding-left: 25px
    +r(1199)
      margin-left: 15px
      margin-right: 7px

    &:before
      margin: -2px
      opacity: 1

  .title
    color: #B4BACA
    font-style: normal
    font-weight: 500
    font-size: 12px
    line-height: 1.17
    letter-spacing: 0.02em

  .mobile-title
    display: none
    margin-bottom: 4px
    color: #B4BACA
    font-style: normal
    font-weight: 500
    font-size: 10px
    line-height: 1.4
    letter-spacing: 0.02em
    +r(767)
      display: block

  .value
    -o-text-overflow: ellipsis
    overflow: hidden
    color: #30393E
    font-style: normal
    font-weight: 400
    font-size: 14px
    line-height: 100%
    letter-spacing: 0.02em
    text-overflow: ellipsis
    white-space: nowrap
    max-width: 100%
    +r(767)
      overflow: visible
      text-overflow: inherit
      white-space: normal

  input.form-control
    -webkit-box-sizing: border-box
    box-sizing: border-box
    width: 80px
    height: 45px
    border: 1.5px solid #E1E3EA
    border-radius: 6px
    color: #30393E
    font-style: normal
    font-weight: 400
    font-size: 14px
    line-height: 1.21
    text-align: center

  .number
    -webkit-box-pack: center
    -ms-flex-pack: center
    -webkit-box-align: center
    -ms-flex-align: center
    -webkit-box-flex: 0
    -ms-flex: 0 0 24px
    display: -webkit-box
    display: -ms-flexbox
    display: flex
    flex: 0 0 24px
    align-items: center
    justify-content: center
    width: 24px
    height: 24px
    margin-right: 3px
    margin-left: 60px
    +r(1199)
      margin-left: 15px
    +r(767)
      margin-left: 10px

    &::before
      margin-top: 2px
      color: #FFDE3F
      font-style: normal
      font-weight: 800
      font-size: 14px
      line-height: 1
      letter-spacing: 0.02em

    &.--numb-1::before
      content: ""

    &.--numb-2::before
      content: ""

    &.--numb-3::before
      content: ""

    &.--numb-4::before
      content: ""

    &.--numb-5::before
      content: ""

    &.--numb-6::before
      content: ""

    &.--numb-7::before
      content: ""

    &.--numb-8::before
      content: ""

    &.--numb-9::before
      content: ""

    &.--numb-10::before
      content: ""

  [data-bs-toggle=collapse]
    cursor: pointer

  .toggle
    -webkit-box-pack: center
    -ms-flex-pack: center
    -webkit-box-align: center
    -ms-flex-align: center
    -webkit-box-flex: 0
    -ms-flex: 0 0 24px
    display: -webkit-box
    display: -ms-flexbox
    display: flex
    flex: 0 0 24px
    align-items: center
    justify-content: center
    width: 24px
    height: 24px
    margin-right: 15px
    cursor: pointer
    +r(767)
      margin-right: 10px

    +icon(plus)

    &::before
      color: #FFDE3F
      font-size: 13px

  [aria-expanded=true] .toggle
    +icon(minus)

    &::before
      color: #628EFF

  .toggle-empty
    -webkit-box-pack: center
    -ms-flex-pack: center
    -webkit-box-align: center
    -ms-flex-align: center
    -webkit-box-flex: 0
    -ms-flex: 0 0 24px
    display: -webkit-box
    display: -ms-flexbox
    display: flex
    flex: 0 0 24px
    align-items: center
    justify-content: center
    width: 24px
    height: 24px
    margin-right: 15px
    background: #EEFAF1
    +r(767)
      margin-right: 10px

    &::before
      -webkit-box-flex: 0
      -ms-flex: 0 0 13px
      -webkit-box-sizing: border-box
      box-sizing: border-box
      display: block
      flex: 0 0 13px
      width: 13px
      height: 13px
      border: 3px solid #57CA70
      border-radius: 50%
      content: ""

.custome-table__body
  .custome-table__row
    padding: 10px 0
    border-bottom: 1px solid #F0F1F4

  .custome-table__cell
    padding-top: 0
    padding-bottom: 0

.custome-table__dropdown
  .custome-table__cell-group
    .custome-table__cell:last-child
      +r(767)
        position: absolute
        right: 0
        bottom: 0

  .custome-table__dropdown-title
    .custome-table__cell-group
      +r(767)
        display: none

.custome-table__dropdown.is-show
  outline: 2px solid #D2D6DF

  .custome-table__cell .toggle-dropdown .value,
  .custome-table__accordion-header .toggle + .value,
  .custome-table__accordion-header .toggle-empty + .value,
  .custome-table__accordion-collapse .check + .value
    font-weight: 500

  .toggle-dropdown-btn
    -webkit-transform: rotate(180deg)
    -ms-transform: rotate(180deg)
    transform: rotate(180deg)
    background: #E0E8FF
    color: #628EFF

  .custome-table__dropdown-title
    .custome-table__cell-group
      +r(767)
        display: block

.custome-table__dropdown-title
  +r(767)
    display: flex
    flex-direction: column
    .custome-table__cell-group
      display: flex
      flex-direction: column
      margin-top: 10px
      margin-left: 70px
      .custome-table__cell
        flex-direction: column
        align-items: flex-start
        border-left: 0
        & + .custome-table__cell
          +r(767)
            margin-top: 8px

.custome-table__dropdown-content
  display: none
  +tr(.25s)
  padding: 0

  .custome-table__cell-group
    +r(767)
      margin-top: 10px
      margin-left: 95px
    .custome-table__cell
      +r(767)
        display: flex
        flex-direction: column
        align-items: flex-start
        border: 0
        & + .custome-table__cell
          margin-top: 8px
        &:last-child
          right: 10px

  .custome-table__row .custome-table__cell
    &:first-child
      padding-left: 30px
      +r(1199)
        padding-left: 10px

    &:last-child
      -webkit-box-flex: 0
      -ms-flex: 0 0 65px
      flex: 0 0 65px
      padding-right: 0

.custome-table__accordion-item
  border: none

  &:last-child .custome-table__accordion-collapse .custome-table__row:last-child
    border-bottom: 0

.custome-table__accordion-collapse
  .custome-table__row
    .custome-table__cell:first-child .value
      margin-left: 125px
      +r(1199)
        margin-left: 80px
      +r(767)
        margin-left: 70px

    .custome-table__cell-group .custome-table__cell .value
      margin-left: 0 !important
      font-weight: 400 !important

  .custome-table__accordion-header
    .custome-table__row
      .custome-table__cell
        .value
          margin-left: 0 !important

.table-before-btn.--menu > *
  right: 30px
  +r(1500)
    right: 10px

.custome-table.custome-table-menu
  .custome-table__row
    grid-template-columns: 40% 60%

  .custome-table__cell-group
    display: grid
    grid-template-columns: calc(100% - 395px) 180px 120px 95px
    position: relative
    +r(1500)
      grid-template-columns: calc(100% - 255px) 100px 100px 55px
    +r(767)
      display: flex
      flex-direction: column
      align-items: flex-start
  .custome-table__dropdown .custome-table__dropdown-title .custome-table__cell-group
    +r(767)
      display: none
  .custome-table__cell
    +r(1500)
      padding: 0 10px

  .custome-table__head .custome-table__cell-group
    +r(767)
      display: none