.product-table
  thead
    th
      padding: 10px 30px
      +r(1499)
        padding: 12px 20px
  tbody
    tr
      td
        font-size: 12px
        padding: 10px 30px
        +r(1499)
          padding: 12px 20px
        &:first-child
          padding-right: 5px
        &:nth-child(2)
          padding-left: 0
          padding-right: 10px
          &::before
            display: none

  .box-product
    display: flex
    align-items: center
    img
      min-width: 80px
      margin-right: 15px
      font-style: normal
      font-weight: normal
      font-size: 12px
      line-height: 120%
      letter-spacing: 0.01em
      color: #30393E
      +r(374)
        min-width: 60px
        max-width: 60px
    a
      font-size: 12px
      +r(374)
        font-size: 10px


.drop-storage
  .drop-button
    .drop-storage__info
      display: none
  &__info
    font-weight: 400
    font-size: 10px
    line-height: 100%
    letter-spacing: 0.02em
    color: #30393E
    border-radius: 4px
    display: inline-block
    margin-top: 5px
    padding: 5px 7px

    &.--red
      background: #FFEFF1

    &.--green
      background: #EEFAF1

.storage-warning
  font-weight: 400
  font-size: 14px
  line-height: 17px
  display: flex
  color: #30393E
  margin-top: 20px

  .ico_warning
    color: #FF5F72
    font-size: 22px
    margin-right: 12px

.button-group-price
  +r(400)
    .button
      width: 100%
      margin-left: 0

.services-img-group
  display: flex
  align-items: center
  justify-content: space-between
  +r(1100)
    flex-direction: column
    justify-content: start
    align-items: start
    .add-img:first-child
      margin-bottom: 30px

.color-button
  text-align: left
  position: relative
  &:focus
    border: 1.5px solid #E1E3EA
  +h
    color: #000
    border: 1.5px solid #B4BACA

  &__preview
    width: 20px
    height: 20px
    position: absolute
    display: block
    top: 50%
    transform: translateY(-50%)
    right: 44px
    border-radius: 50%

  &::after
    content: ''
    display: block
    background-image: url('data:image/svg+xml;utf8,<svg width="21" height="20" viewBox="0 0 21 20" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M6.62192 18.9997C9.72682 18.9997 12.2438 16.4827 12.2438 13.3778C12.2438 10.2729 9.72682 7.75586 6.62192 7.75586C3.51702 7.75586 1 10.2729 1 13.3778C1 16.4827 3.51702 18.9997 6.62192 18.9997Z" stroke="%23C3C8D5" stroke-width="1.5" stroke-miterlimit="10" stroke-linecap="round" stroke-linejoin="round"/><path d="M13.8211 18.9997C16.926 18.9997 19.4431 16.4827 19.4431 13.3778C19.4431 10.2729 16.926 7.75586 13.8211 7.75586C10.7162 7.75586 8.19922 10.2729 8.19922 13.3778C8.19922 16.4827 10.7162 18.9997 13.8211 18.9997Z" stroke="%23C3C8D5" stroke-width="1.5" stroke-miterlimit="10" stroke-linecap="round" stroke-linejoin="round"/><path d="M10.2215 12.2438C13.3264 12.2438 15.8434 9.72682 15.8434 6.62192C15.8434 3.51702 13.3264 1 10.2215 1C7.11663 1 4.59961 3.51702 4.59961 6.62192C4.59961 9.72682 7.11663 12.2438 10.2215 12.2438Z" stroke="%23C3C8D5" stroke-width="1.5" stroke-miterlimit="10" stroke-linecap="round" stroke-linejoin="round"/></svg>')
