.orders-filter
  width: 100%
  display: grid
  grid-auto-rows: auto
  grid-template-columns: repeat(5, 1fr)
  gap: 10px
  margin-bottom: 30px
  +r(1499)
    grid-template-columns: repeat(3, 1fr)
  +r(992)
    grid-template-columns: repeat(2, 1fr)
  +r(992)
    grid-template-columns: repeat(2, 1fr)
  +r(568)
    grid-template-columns: repeat(1, 1fr)
  +rmin(1500)
    .input-group
      position: relative

      .clear-date
        position: absolute
        left: 100%
        top: 0

.table-filters 
  display: flex
  gap: 20px

  +r(768)
    flex: 0 0 100%
    margin-top: 15px
    order: 1
    > div
      flex: 0 0 calc( 50% - 10px )

  +r(568)
    flex-wrap: wrap
    > div
      flex: 0 0 100%


.order-link
  color: #628EFF
  +h
    color: #30393E

.order-info
  display: block!important
  font-weight: 400
  font-size: 12px
  line-height: 100%
  letter-spacing: 0.02em
  color: #878C98
  margin-top: 3px

table.footable tbody td
  .order-customer-phone
    display: block
    margin-top: 3px
    font-weight: 400
    font-size: 12px
    line-height: 100%
    letter-spacing: 0.02em
    color: #878C98

.order-head
  display: flex
  justify-content: space-between
  align-items: center
  margin-top: 15px
  margin-bottom: 20px

  h4
    margin: 0

  +r(768)
    flex-direction: column
    justify-content: flex-start
    align-items: flex-start
    h4
      margin-bottom: 15px

.order-rating-cont 
  display: flex
  align-items: center

  .order-rating
    margin-left: 10px
.order-rating-label
  font-weight: 500
  font-size: 12px
  line-height: 100%
  color: #B4BACA

.order-rating
  display: flex
  gap: 10px
  flex-shrink: 1
  &__count
    font-weight: 500
    font-size: 20px
    line-height: 24px
    color: #30393E
  &__list
    margin: 0
    padding: 0
    list-style: none
    display: flex
    flex-wrap: wrap
    align-items: center

    li
      margin-left: 2px
      display: block
      width: 7px
      height: 10px
      background: #F0F1F4
      border-radius: 2px

      &._active
        background: #68CE67
      
      &.--red
        background: #FF5F72

      &.--yellow
        background: #FFDE3F

      &:first-child
        margin-left: 0

.order
  max-width: 870px
  flex: 1 1 auto
  background: #F8F9FA
  padding: 30px
  +r(767)
    padding: 30px 20px
  +r(567)
    padding: 20px
  
  &__col
    flex: 0 0 33.33%
  
  +r(768)
    flex-wrap: wrap
    &__col
      flex: 0 0 100%
      max-width: 250px

.info-item
  margin-bottom: 10px
  &__label
    font-weight: 500
    font-size: 12px
    line-height: 100%
    color: #B4BACA
    margin-bottom: 5px
  &__data
    font-weight: 400
    font-size: 14px
    line-height: 100%
    letter-spacing: 0.02em
    color: #30393E

    p
      margin: 0
      font-size: 12px
      letter-spacing: 0.02em
      line-height: 120%

  a
    &::after
      content:', '
      color: #30393E
    &:last-child
      &::after
        content:''

  .order-rating__count 
    font-size: 14px
    line-height: 1

.order-history
  max-height: 257px
  overflow: hidden
  +tr(.25s)
  display: flex
  flex-wrap: wrap
  margin: -8px 0 0 0

  &__item
    padding: 9px 0
    border-bottom: 1px solid transparent
    flex: 0 0 33.333%
    position: relative
    +r(767)
      flex: 0 0 50%

      &::after
        width: calc( 100% - 30px )
    +r(567)
      flex: 0 0 100%

      &::after
        width: 100%

    &::after
      content: ''
      width: calc( 100% - 60px )
      left: 0
      bottom: 0
      height: 1px
      background: #F0F1F4
      position: absolute
      display: block

  &__name
    font-weight: 400
    font-size: 14px
    line-height: 100%
    letter-spacing: 0.02em
    color: #30393E
    margin-bottom: 6px

  &__time
    font-weight: 400
    font-size: 12px
    line-height: 100%
    letter-spacing: 0.02em
    color: #878C98

  &__more
    font-weight: 400
    font-size: 14px
    line-height: 100%
    letter-spacing: 0.02em
    color: #628EFF
    padding: 0
    border: 0
    float: right
    width: 33.333%
    text-align: left
    margin-top: -51px
    height: 50px
    background: #F8F9FA
    display: block
    position: relative
    display: flex
    align-items: flex-end
    +tr(.25s)

    +r(767)
      width: 50%
    +r(567)
      width: 100%
      margin: 0
      float: none
      height: 30px
      justify-content: flex-end

    .--active
      display: none

    &.active
      margin-top: 0
      height: 20px
      +r(567)
        height: 30px
      .--close
        display: none
      .--active
        display: inline

.order-table-title
  font-size: 20px

.order-name
  font-weight: 400
  font-size: 12px
  line-height: 100%
  color: #878C98
  display: block!important

.comment
  position: relative
  +h
    z-index: 5
    .comment__dropdown
      opacity: 1
      visibility: visible
      pointer-events: all

  &__dropdown 
    position: absolute
    width: 275px
    background: #FFFFFF
    box-shadow: 0px 5px 15px -1px rgba(163, 174, 208, 0.3)
    border-radius: 15px 15px 15px 0px
    padding: 20px
    left: 0
    bottom: 0
    z-index: 3
    display: flex
    +tr(0.25s)
    opacity: 0
    visibility: hidden
    pointer-events: none

    .ico_reviwe
      width: 25px
      height: 25px
      font-size: 22px
      color: #628EFF
      margin-right: 10px

  &__text
    font-weight: 400
    font-size: 14px
    line-height: 120%
    color: #30393E
    flex: 0 0 200px
    text-align: left

.status-button
  &-1
    --status-color-light: #CDEFD4
    --status-color: #57CA70
  &-2
    --status-color-light: #FFD3D3
    --status-color: #FF6A6A
  &-3
    --status-color-light: #FEE5B4
    --status-color: #FBA705
  &-4
    --status-color-light: #D0DEFF
    --status-color: #628EFF
  &-5
    --status-color-light: #DFDBFF
    --status-color: #9588FF
  &-6
    --status-color-light: #E2E4E7
    --status-color: #9FA4B0

.status-candle
  &-1
    --status-color-light: #CDEFD4
    --status-color: #57CA70
  &-2
    --status-color-light: #FFD3D3
    --status-color: #FF6A6A
  &-3
    --status-color-light: #FEE5B4
    --status-color: #FBA705
  &-4
    --status-color-light: #D0DEFF
    --status-color: #628EFF
  &-5
    --status-color-light: #DFDBFF
    --status-color: #9588FF
  &-6
    --status-color-light: #E2E4E7
    --status-color: #9FA4B0

.status-button
  padding: 11px 10px
  gap: 5px
  height: 35px
  border: 2px solid var(--status-color-light)
  border-radius: 6px
  font-weight: 400
  font-size: 12px
  line-height: 100%
  display: inline-flex
  align-items: center
  color: #30393E
  white-space: nowrap

  &::before
    width: 13px
    height: 13px
    flex: 0 0 13px
    border-radius: 50%
    background: var(--status-color)
    border: 4px solid var(--status-color-light)
    content: ''

table.table-order tbody td
  font-size: 12px
  padding: 10px 10px
  &:first-child
    padding: 10px 10px 10px 20px

table.table-order thead tr th
  font-size: 10px

+r(1499)
  table.table-order tbody td 
    padding: 10px 10px


table.table-order thead tr th:first-child
  padding: 10px 10px 10px 20px

.status-item
  padding-right: 35px
  position: relative
  max-width: 125px

.status-candle
  font-weight: 400
  font-size: 12px
  line-height: 100%
  display: flex
  align-items: center
  color: #30393E
  margin-bottom: 3px

  &::before
    width: 13px
    height: 13px
    flex: 0 0 13px
    border-radius: 50%
    background: var(--status-color)
    border: 4px solid var(--status-color-light)
    content: ''
    margin-right: 5px

.status-time
  font-weight: 400
  font-size: 12px
  line-height: 100%
  color: #878C98
  padding-left: 18px
  margin-bottom: 3px

.status-master
  font-weight: 400
  font-size: 12px
  line-height: 100%
  color: #878C98
  padding-left: 18px

.status-info
  padding: 0
  border: 0
  color: #B4BACA!important
  font-size: 14px
  position: absolute
  right: 0
  top: 0

  +h
    color: #628EFF!important


.staus-histoty
  max-height: 216px
  overflow-y: auto
  scrollbar-color: #C3C8D5 #F0F1F4
  scrollbar-width: thin!important
  -moz-appearance: none !important

  &::-webkit-scrollbar
    width: 4px

  &::-webkit-scrollbar-track
    background-color: #F0F1F4

  &::-webkit-scrollbar-thumb
    background-color: #C3C8D5

  &__head 
    display: flex
    flex-wrap: wrap
    border-bottom: 1px solid #F0F1F4
    padding: 10px

    .staus-histoty__col
      font-weight: 500
      font-size: 12px
      line-height: 100%
      color: #B4BACA
  &__col
    font-weight: 400
    font-size: 12px
    line-height: 100%
    color: #30393E
    flex: 0 0 33.33%
    &:last-child
      text-align: right
  &__row
    display: flex
    flex-wrap: wrap
    align-items: center
    padding: 10px
    border-bottom: 1px solid #F0F1F4
    &:last-child
      border-bottom: 0
  
  &__name
    font-weight: 400
    font-size: 12px
    line-height: 100%
    color: #878C98

.order-payment
  margin-left: auto
  padding: 30px
  max-width: 225px
  width: 100%
  background: #F8F9FA
  +r(991)
    order: 1
    margin-bottom: 20px
    margin-left: 0
  +r(767)
    padding: 20px
  &__item
    margin-bottom: 12px
  &__title
    font-weight: 500
    font-size: 12px
    line-height: 100%
    color: #B4BACA
    margin-bottom: 5px
  &__status
    display: flex
    align-items: center
    justify-content: space-between
    font-weight: 400
    font-size: 12px
    line-height: 100%
    letter-spacing: 0.02em
    color: #30393E
  &__date
    display: block
    span 
      display: block
  &__type
    img
      max-width: 100%
      display: block
  &__text 
    font-weight: 400
    font-size: 14px
    line-height: 100%
    letter-spacing: 0.02em
    color: #30393E
  .button
    width: 100%
    padding: 0 15px

.order-tabs
  padding-bottom: 0
  flex: 0 0 100%
  max-width: 100%
  +r(991)
    flex: 0 0 auto
    order: 2

.page-order-wrapper
  display: flex
  flex-wrap: wrap
  +r(991)
    flex-direction: column

.order-tab-content
  flex: 0 0 calc(100% - 226px)
  padding-right: 74px
  margin-top: 0

  +r(1400)
    padding-right: 30px

  +r(991)
    order: 3
    flex: 0 0 auto
    padding-right: 0

  .tab-pane
    height: 100%
  .order
    height: 100%

.orders
  .page-bottom-group .button-group
    margin-top: 0
    +r(567)

      > *
        margin: 0 0 5px 0
        width: 100%

.order-feedback
  display: flex
  justify-content: space-between
  margin-bottom: 20px

  .button
    height: 35px

  &__number
    font-weight: 500
    font-size: 16px
    line-height: 100%
    color: #30393E

  > *
    max-width: 50%

.order-feedback-rating
  display: flex
  align-items: center

  .form-label
    margin-bottom: 0
    margin-right: 15px

  .rating
    margin-bottom: 0