.form-group
  margin-bottom: 30px
  +r(767)
    margin-bottom: 20px

.form-group.--textarea
  margin-bottom: 25px
  +r(767)
    margin-bottom: 20px

.form-control
  display: inline-block
  height: 45px
  line-height: 42px
  padding: 0 15px
  background: #FFFFFF
  border: 1.5px solid #E1E3EA
  box-sizing: border-box
  border-radius: 6px

  font-style: normal
  font-weight: 100
  font-size: 14px
  color: #000

  +line(1)

  &:focus
    border: 1.5px solid #B4BACA
    +placeholder()
      opacity: 0

  +placeholder()
    color: #C3C8D5
    text-overflow: ellipsis
    opacity: 1
    transition: opacity 0.3s ease
    font-style: normal
    font-weight: 100
    font-size: 14px

textarea.form-control
  vertical-align: top
  height: 90px
  line-height: 1.3
  padding-top: 15px
  resize: none

.input-group-text
  display: flex
  justify-content: center
  align-items: center
  background: none
  border: 1.5px solid #E1E3EA
  border-right: 0
  min-width: 60px

  font-style: normal
  font-weight: normal
  font-size: 14px
  line-height: 17px
  color: #C3C8D5

  +fz(14, 12)

.nav-pills .nav-item
  margin-right: 10px
  &:last-child
    margin-right: 0

.nav-pills .nav-link
  display: flex
  justify-content: center
  align-items: center
  padding: 0 15px
  border: 2px solid #E1E3EA
  box-sizing: border-box
  border-radius: 30px
  height: 30px
  font-style: normal
  font-weight: normal
  font-size: 14px
  line-height: 1.1
  color: #30393E

.nav-pills .nav-link.active,
.nav-pills .show>.nav-link
  border: 2px solid $yellow
  color: #30393E
  background: none

.tab-content
  margin-top: 25px

