.invalid-feedback
  width: auto
  height: 20px
  position: absolute
  top: -10px
  right: -10px
  background: #FF5F72
  font-style: normal
  font-weight: 500
  font-size: 12px
  line-height: 20px
  letter-spacing: 0.01em
  color: #FFFFFF
  padding: 0 10px
  margin: 0

  &.is-show
    display: block

.form-control:disabled,
.form-control[readonly]
  background-color: #fff
  opacity: 1


.form-label
  font-weight: 400
  font-size: 12px
  line-height: 100%
  color: #B4BACA
  margin-bottom: 8px