.search
  display: block
  position: relative
  margin-bottom: 20px

  .search__label + .search__controls
    border-radius: 0 6px 6px 0
  &.is-active
    z-index: 1001
    .search__label
      background: #B4BACA
      +h
        background: $yellow
        cursor: pointer
    +r(767)
      position: fixed
      top: 0
      left: 0
      width: 100%
      height: 100vh
      overflow-y: auto
      background-color: rgba(#000, 0.65)
      padding: 30px 15px 30px
      box-sizing: border-box
      .search__head
        display: flex
      .search__footer
        display: block

  &__head
    display: none
    justify-content: space-between
    align-items: center
    padding: 10px 0
    margin-bottom: 20px
  &__title
    display: flex
    align-items: center
    font-style: normal
    font-weight: bold
    font-size: 16px
    line-height: 1.25
    color: #000000
  &__close
    display: flex
    justify-content: center
    align-items: center
    width: 40px
    height: 40px
    border-radius: 50%
    background: $accent
    color: #fff
    cursor: pointer
  &__body
    display: flex
    position: relative
  &__footer
    margin-top: 30px
    display: none
    .uk-button
      width: 100%
      &+ .uk-button
        margin-top: 10px

  &__label
    display: flex
    justify-content: center
    align-items: center
    width: 45px
    height: 45px
    background: #E1E3EA
    border-radius: 6px 0px 0px 6px
    padding: 0 10px
    box-sizing: border-box
    color: #fff
  &__controls
    display: flex
    justify-content: space-between
    align-items: center
    text-align: center
    width: 100%
    height: 45px
    background-image: url("data:image/svg+xml,%3Csvg width='20' height='20' viewBox='0 0 20 20' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle fill='none' stroke='%23DCDCDC' stroke-width='1.1' cx='9' cy='9' r='7'%3E%3C/circle%3E%3Cpath fill='none' stroke='%23DCDCDC' stroke-width='1.1' d='M14,14 L18,18 L14,14 Z'%3E%3C/path%3E%3C/svg%3E")
    background-repeat: no-repeat
    background-position: left 10px center
    background: #FFFFFF
    border: 1px solid #e1e3ea
    box-sizing: border-box
    border-radius: 6px
    padding-left: 45px
    padding-right: 10px

    +placeholder()
      text-overflow: ellipsis
      white-space: nowrap
      overflow: hidden

    input
      border: 0
      width: 100%
      height: 45px
      line-height: 45px
      outline: 0
      background-color: transparent
      margin-right: 10px
      font-style: normal
      font-weight: 100
      font-size: 14px
      color: #30393E
      text-overflow: ellipsis
      +r(767)
        box-sizing: border-box
        margin: 0
        padding: 0
        height: 30px
        line-height: 30px
      +placeholder()
        font-style: normal
        font-weight: 100
        font-size: 14px
        color: #C3C8D5

    .tagger
      border: 0
      background: none
      padding-top: 5px
      padding-left: 0
      flex-grow: 1

    .tagger > ul > li:not(.tagger-new) > a,
    .tagger > ul > li:not(.tagger-new) > span
      height: 22px
      padding: 0 8px
      border-radius: 4px
      background: #FFEFF1
      border: none

      align-items: center
      color: #30393E
      font-size: 12px
      font-style: normal
      font-weight: normal
      letter-spacing: 0.02em
      line-height: 100%

      .close
        display: inline-block
        margin-left: 3px
        margin-top: -2px
        color: #FF5F72
        font-size: 20px
        padding: 0

  &__result
    display: flex
    justify-content: space-between
    align-items: flex-start
    margin-bottom: 14px
    +tr(0.5s)
    +r(767)
      display: block

  &__result-box
    display: flex
    justify-content: flex-start
    align-items: center
    flex-wrap: wrap
    max-height: 115px
    overflow-y: auto
    margin-right: 30px

    scrollbar-color: #C3C8D5 #F0F1F4
    scrollbar-width: thin!important
    -moz-appearance: none !important
    &::-webkit-scrollbar
      width: 4px
    &::-webkit-scrollbar-track
      background-color: #F0F1F4
    &::-webkit-scrollbar-thumb
      background-color: #C3C8D5

    +r(767)
      margin-right: 0
      margin-bottom: 20px

    .tagger
      padding: 0
      border: 0

    .tagger .tagger-new input
      min-width: 200px

  &__result-btns
    +r(767)
      display: flex
      justify-content: flex-end
      .result-select 
        margin-top: 0
        margin-left: 5px

  &__drop
    display: block
    position: absolute
    top: calc(100% + 5px)
    left: 0
    padding: 20px 15px
    width: 100%
    box-sizing: border-box
    background-color: #fff
    border: 1px solid #F2F2F2
    box-sizing: border-box
    border-radius: 6px
    opacity: 0
    pointer-events: none
    z-index: -1
    +r(1199)
      left: 0
      width: 100%

    &.is-show
      opacity: 1
      z-index: 2
      pointer-events: all

  &__items
    table
      margin: 0

    table.footable
      tbody
        td:first-child
          padding-left: 0

  &__btn
    display: flex
    justify-content: flex-end
    align-items: center
    flex-wrap: wrap
    margin-top: 20px
    +r(767)
      flex-direction: column-reverse
      align-items: flex-end
    & > *
      width: 30%
      +r(1199)
        width: auto
      &:first-child
        +r(1199)
          display: none
    .table-nav
      +r(767)
        margin-bottom: 20px
      .drop
        min-width: 100px

.result-list
  display: flex
  justify-content: flex-start
  align-items: center
  list-style-type: none
  padding: 0
  margin: 0
  li
    +r(767)
      margin-right: 12px
      margin-bottom: 10px
    & + li
      margin-left: 5px
      +r(767)
        margin-left: 0

.result-item
  display: flex
  align-items: center
  background: #FFEFF1
  border: 1px solid #FFEFF1
  box-sizing: border-box
  border-radius: 4px
  padding: 0 8px
  height: 22px
  margin: 0 5px 5px 0
  +tr(0.25s)
  +h
    border: 1px solid #FF5F72
  &__title
    font-style: normal
    font-weight: normal
    font-size: 12px
    line-height: 100%
    align-items: center
    letter-spacing: 0.02em
    color: #30393E
  &__del
    margin-left: 3px
    font-size: 10px
    cursor: pointer
    display: flex
    justify-content: center
    align-items: center
    color: #FF5F72
    +tr(0.3s)
    +h
      color: red

.result-clear
  display: flex
  align-items: center
  min-width: 80px
  height: 22px
  background: #FF5F72
  box-sizing: border-box
  border-radius: 4px
  padding: 0 8px
  font-style: normal
  font-weight: normal
  font-size: 12px
  letter-spacing: 0.02em
  color: #FFFFFF
  cursor: pointer
  white-space: nowrap
  +tr(0.3s)
  +r(767)
    margin-left: 0
  +h
    background-color: #FF5F72
    color: #fff

.result-select
  display: flex
  align-items: center
  min-width: 80px
  height: 22px
  background: #628EFF
  box-sizing: border-box
  border-radius: 4px
  padding: 0 8px
  font-style: normal
  font-weight: normal
  font-size: 12px
  letter-spacing: 0.02em
  color: #FFFFFF
  cursor: pointer
  white-space: nowrap
  margin-top: 5px
  +tr(0.3s)
  +h
    background-color: darken(#628EFF, 10%)
    color: #fff

.search__overlay
  display: block
  position: fixed
  top: 0
  left: 0
  width: 100%
  height: 100%
  background: rgba(90, 93, 101, 0.6)
  z-index: -1
  opacity: 0
  pointer-events: none
  +tr(0.5s)
  &.is-show
    opacity: 1
    z-index: 1000


.block-editor
  display: block
  position: relative
  border: 1.5px solid #E1E3EA
  box-sizing: border-box
  border-radius: 6px
  overflow: hidden
  margin-bottom: 20px
  &:focus
    border: 1.5px solid #B4BACA

  .block-editor-head
    background: #f2f2f2
  .block-editor-body
    padding: 30px
    font-style: normal
    font-weight: normal
    font-size: 14px
    line-height: 140%
    letter-spacing: 0.02em
    color: #30393E
    +r(767)
      padding: 10px 10px 15px
    a
      font-style: normal
      font-weight: normal
      font-size: 14px
      line-height: 1.71
      text-decoration-line: underline
      color: $accent
      +tr(0.3s)
      +h
        text-decoration: none

    ul, ol
      li
        font-style: normal
        font-weight: normal
        font-size: 14px
        line-height: 1.71
        color: $accent

    .align-center
      text-align: center

    .accent-text
      font-weight: bold
      font-size: 18px
      +fz(18, 14)
      line-height: 1.83
      text-align: center
      color: #236FA1

.block-editor-save
  .uk-button
    padding: 0 50px

.search.--old-search
  .search__controls
    border-radius: 0 6px 6px 0
    background-image: none
    padding-left: 15px
    .search__result
      margin-bottom: 0
  .result-item
    margin-bottom: 0
  .result-clear
    width: auto