.modal-backdrop
  background: #25282B
  opacity: 0.98!important

.modal-content
  padding: 50px 64px
  position: relative
  background: #FFFFFF
  box-shadow: 0px 10px 30px -20px rgba(135, 140, 152, 0.4)
  border-radius: 20px
  +r(767)
    padding: 16px
    border-radius: 10px

  .btn-close
    position: absolute
    top: 20px
    right: 20px
    background-size: 14px

.modal-header
  display: block
  padding: 0
  margin-bottom: 30px
  border: 0
  +r(767)
    padding: 0 20px

.modal-title
  font-style: normal
  font-weight: 500
  font-size: 20px
  line-height: 24px
  text-align: center
  color: #30393E

.modal-body
  padding: 0
  border: 0
  .form-group
    margin-bottom: 20px

  .mt-4
    margin-top: 30px!important

.modal-overflow
  overflow: auto
  max-height: 340px
  scrollbar-color: #C3C8D5 #F0F1F4
  scrollbar-width: thin!important
  -moz-appearance: none !important
  padding: 10px 0

  &::-webkit-scrollbar
    width: 4px
  &::-webkit-scrollbar-track
    background-color: #F0F1F4
  &::-webkit-scrollbar-thumb
    background-color: #C3C8D5

  +r(767)
    max-height: 210px

  .form-group
    margin-bottom: 5px

#m-edit-review
  textarea
    min-height: 180px

#m-select-date

  @media (min-width: 767px)
    .modal-dialog
      max-width: 535px

  @media (min-width: 319px)
    .modal-dialog
      max-width: 280px
      margin: 0 auto

  .modal-content
    padding: 15px
    border-radius: 12px
    background: #FFFFFF
    box-shadow: 0px 21px 38px 20px rgba(48, 57, 62, 0.3)

.calendars
  display: flex
  align-items: stretch

  .calendar
    display: flex
    flex-direction: column
    justify-content: space-between

    & + .calendar
      margin-left: 5px
      +r(767)
        display: none

    .datepicker
      padding: 0
      border: 0

      .datepicker--content
        padding: 4px 0

  .calendar-bottom
    display: flex
    align-items: center
    height: 40px
    padding: 0 15px
    background: #F0F1F4

    font-style: normal
    font-weight: 500
    font-size: 14px
    line-height: 100%
    letter-spacing: 0.02em
    color: #B4BACA

.modal-message
  .modal-content
    padding: 35px 65px
    +r(767)
      padding: 35px 35px
  .modal-header
    margin: 0
  .modal-body
    p
      display: flex
      align-items: center
      margin: 0

      font-style: normal
      font-weight: 500
      font-size: 16px
      line-height: 19px
      color: #30393E

      +ico()
        width: 24px
        height: 24px
        font-size: 24px
        margin-right: 15px

    .button
      height: 50px

#m-success
  .modal-body
    +ico()
      color: #57CA70

#m-error
  .modal-body
    +ico()
      color: #FF5F72



