.m-edit-categories-tag
  .modal-title
    +r(767)
      margin-top: 20px

.m-add-lang
  .check__box
    font-size: 14px

.m-edit-review
  .rating
    font-size: 10px
  .list-info
    margin-bottom: 10px

.m-select-date
  .modal-content
    .btn-close
      top: 15px
      right: 15px

.m-add-banner_tab
  & > span
    width: 40%
    line-height: 1.1

.custome-accordion
  .custome-accordion-item
    display: block
    position: relative
    background: none
    border: none
    margin-bottom: 20px
    &.--open
      .custome-accordion-button:after
        transform: rotate(180deg)
  .custome-accordion-button
    display: flex
    justify-content: space-between
    align-items: center
    width: 100%
    padding: 0
    font-style: normal
    font-weight: 500
    font-size: 16px
    line-height: 19px
    color: #30393E
    background: none
    border: none
    box-shadow: none
    margin-bottom: 15px
    &:after
      content: ''
      display: flex
      justify-content: center
      align-items: center
      width: 30px
      height: 30px
      flex: 0 0 30px
      background-color: #F8F9FA
      background-image: url("data:image/svg+xml,%3Csvg width='12' height='8' viewBox='0 0 12 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M6 6.5L5.29289 7.20711L6 7.91421L6.70711 7.20711L6 6.5ZM0.292893 2.20711L5.29289 7.20711L6.70711 5.79289L1.70711 0.792893L0.292893 2.20711ZM6.70711 7.20711L11.7071 2.20711L10.2929 0.792894L5.29289 5.79289L6.70711 7.20711Z' fill='%23B4BACA'/%3E%3C/svg%3E")
      background-repeat: no-repeat
      background-size: 12px
      background-position: center
      border-radius: 6px
      margin-left: auto
  .custome-accordion-content
    display: none
    .form-group
      margin-bottom: 15px

.modal-color-picker
  +rmin(640)
    max-width: 640px
    
    .modal-content
      padding: 50px

.select-color-container
  margin-bottom: 30px

  .IroColorPicker
    +rmin(640)
      grid-temlate-rows: 143px 40px 67px
      grid-temlate-columns: 250px 260px
      column-gap: 30px
    +r(639)
      justify-items: center
  .IroWheel
    +rmin(640)
      grid-row: span 3
      grid-column: 1 / 1
    +r(639)
      margin-bottom: 20px
    .IroHandle
      circle
        stroke: #fff

  .IroSlider
    height: 22px!important
    border: 1px solid #F0F1F4!important
    border-radius: 30px!important
    padding: 1px
    box-shadow: 0 0 0 2px #FFFFFF inset
    +rmin(640)
      grid-row: 2 / 2
      grid-column: 2 / 2
      margin-bottom: 15px
    +r(639)
      margin-bottom: 20px

    .IroHandle
      top: -20px!important
      circle
        stroke: #fff
        stroke-width: 5
        &:first-child
          stroke: #D2D6DF
          stroke-width: 2

    &:last-child
      margin-bottom: 43px
      +r(639)
        margin-bottom: 0
      +rmin(640)
        grid-row: 3 / 3
        grid-column: 2 / 2

.select-color-nav
  position: absolute
  display: flex
  flex-wrap: wrap
  width: 260px
  right: 0
  top: 43px
  +r(639)
    position: static
    justify-content: center
    margin: auto
    margin-bottom: 20px

.select-color-previve
  background: #D4014C
  border-radius: 60px
  width: 70px
  height: 70px
  position: relative

  &__inner
    position: absolute
    background: #D4014C
    opacity: 0.6
    filter: blur(10px)
    border-radius: 60px
    width: 60px
    height: 60px
    bottom: -4px
    left: 5px


.select-color-input
  border: 2px solid #E1E3EA
  border-radius: 100vw
  font-weight: 500
  font-size: 20px
  line-height: 100%
  text-align: center
  color: #30393E
  width: 180px
  height: 70px
  margin-left: 10px

.modal-select-overflow
  max-height: 207px
  overflow-y: auto
  scrollbar-color: #C3C8D5 #F0F1F4
  scrollbar-width: thin!important
  -moz-appearance: none !important
  margin: 10px 0
  padding: 3px 0

  &::-webkit-scrollbar
    width: 4px
  &::-webkit-scrollbar-track
    background-color: #F0F1F4
  &::-webkit-scrollbar-thumb
    background-color: #C3C8D5

.modal-select-list
  margin: 15px 0
  padding: 0
  list-style: none

  li
    margin-bottom: 10px
  
  li:last-child
    margin-bottom: 0

.modal-select-add-group
  margin-top: 5px
  +rmin(567)
    padding-left: 35px

.modal-select
  +rmin(575)
    max-width: 640px
  .modal-content
    padding: 20px

.modal-select-input
  border: 0!important
  border-bottom: 1px solid #E1E3EA!important
  border-radius: 0

.modal-photo-full
  +rmin(840)
    max-width: 840px