.counter
  display: inline-flex
  justify-content: flex-start
  align-items: center
  position: relative
  width: 100%
  max-width: 200px
  flex: 0 0 200px
  padding: 0 40px 0 0
  overflow: hidden
  box-sizing: border-box
  user-select: none
  background-color: #fff
  border: 1.5px solid #E1E3EA
  border-radius: 6px

  +r(1199)
    max-width: 140px
    flex: 0 0 140px

  &__field
    -webkit-box-shadow: none
    width: 120px
    border: none
    border-top-right-radius: 0
    border-bottom-right-radius: 0
    box-shadow: none
    user-select: none

    +r(1199)
      width: 60px

    input
      -moz-appearance: textfield
      position: relative
      max-width: 120px
      height: 40px
      border: 0
      font-style: normal
      font-weight: normal
      font-size: 15px
      line-height: 130%
      color: #050505
      text-align: center
      user-select: none

      +r(1199)
        max-width: 60px

      &:hover, &:focus, &:active
        outline: 0

      &[type=number]
        &::-webkit-inner-spin-button,
        &::-webkit-outer-spin-buttons
          -webkit-appearance: none
          margin: 0

  &__btn
    display: flex
    justify-content: center
    align-items: center
    width: 40px
    height: 40px
    flex: 0 0 40px
    overflow: hidden
    border: none
    box-shadow: none
    user-select: none
    color: #fff
    cursor: pointer

    +h
      background: #F0F1F4

    &:after
      content: ''
      display: flex
      align-items: center
      justify-content: center
      top: 0
      right: 0
      bottom: 0
      left: 0
      width: 40px
      height: 40px
      border: none
      text-shadow: none
      background-repeat: no-repeat
      background-position: center
      background-size: 10px
      +tr(0.3s)

    &.minus
      border-right: 1.5px solid #E1E3EA
      &:after
        background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 9 3' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.583557 1.5351C0.583557 0.98281 1.03127 0.535095 1.58356 0.535095H7.58356C8.13584 0.535095 8.58356 0.98281 8.58356 1.5351C8.58356 2.08738 8.13584 2.5351 7.58356 2.5351H1.58356C1.03127 2.5351 0.583557 2.08738 0.583557 1.5351Z' fill='black'/%3E%3C/svg%3E%0A")

    &.plus
      border-left: 1.5px solid #E1E3EA
      &:after
        background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 9 9' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.583557 4.53511C0.583557 4.09328 0.941729 3.73511 1.38356 3.73511H7.78356C8.22539 3.73511 8.58356 4.09328 8.58356 4.53511C8.58356 4.97694 8.22539 5.33511 7.78356 5.33511H1.38356C0.941729 5.33511 0.583557 4.97694 0.583557 4.53511Z' fill='black'/%3E%3Cpath d='M4.58356 0.53511C5.02538 0.53511 5.38356 0.893283 5.38356 1.33511L5.38356 7.73511C5.38356 8.17694 5.02538 8.53511 4.58356 8.53511C4.14173 8.53511 3.78356 8.17694 3.78356 7.73511L3.78356 1.33511C3.78356 0.893282 4.14173 0.53511 4.58356 0.53511Z' fill='black'/%3E%3C/svg%3E%0A")


  &.--small
    max-width: 90px
    flex: 0 0 90px
    height: 50px
    +r(767)
      height: 40px
    .counter__field
      width: 63px
      height: 50px
      flex: 0 0 63px
      +r(767)
        height: 40px
      input
        width: 63px
        height: 50px
        flex: 0 0 63px
        border-right: 1.5px solid #E1E3EA
        +r(767)
          height: 40px
    .counter__btn
      width: 25px
      height: 25px
      flex: 0 0 25px
      position: absolute
      right: 0
      border: none
      +r(767)
        width: 25px
        height: 20px
      &:after
        width: 25px
        height: 25px
        +r(767)
          width: 25px
          height: 20px
      &.plus
        top: 0
      &.minus
        top: 24px
        border-top: 1.5px solid #E1E3EA
        +r(767)
          top: 19px

.form-control-counter
  width: 100%
  max-width: calc(100% - 290px)
  display: flex
  flex: 0 0 100%
  padding: 0
  +r(1199)
    max-width: calc(100% - 249px)
    flex: 0 0 100%
  +r(767)
    max-width: 60%
    flex: 0 0 60%
  .counter__field
    width: auto
    max-width: 100%
    flex-grow: 1
    input
      width: 100%
      max-width: 100%
      padding-left: 15px
      text-align: left
      font-weight: 100
      color: #000
  .counter__btn
    width: 20px
    height: 14px
    background: #F8F9FA
    border: none
    display: flex
    justify-content: center
    align-items: center
    position: absolute
    right: 10px
    &.minus
      top: 5px
      &:after
        background-image: url("data:image/svg+xml,%3Csvg width='12' height='8' viewBox='0 0 12 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M6 6.5L5.29289 7.20711L6 7.91421L6.70711 7.20711L6 6.5ZM0.292893 2.20711L5.29289 7.20711L6.70711 5.79289L1.70711 0.792893L0.292893 2.20711ZM6.70711 7.20711L11.7071 2.20711L10.2929 0.792894L5.29289 5.79289L6.70711 7.20711Z' fill='%23B4BACA'/%3E%3C/svg%3E")
        background-size: 8px
        transform: rotate(180deg)
    &.plus
      top: 21px
      &:after
        background-image: url("data:image/svg+xml,%3Csvg width='12' height='8' viewBox='0 0 12 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M6 6.5L5.29289 7.20711L6 7.91421L6.70711 7.20711L6 6.5ZM0.292893 2.20711L5.29289 7.20711L6.70711 5.79289L1.70711 0.792893L0.292893 2.20711ZM6.70711 7.20711L11.7071 2.20711L10.2929 0.792894L5.29289 5.79289L6.70711 7.20711Z' fill='%23B4BACA'/%3E%3C/svg%3E")
        background-size: 8px