.schedule-head
  display: flex
  justify-content: space-between
  align-items: center
  margin-top: 30px

  +r(767)
    margin-top: 20px
    
  +r(568)
    flex-direction: column

    .button
      height: 35px
      margin-top: 20px
      width: 100%

  .button
    height: 35px

.schedule-info
  display: flex
  align-items: center
  margin: 0
  padding: 0
  list-style: none

  &__item
    font-weight: 500
    font-size: 14px
    line-height: 17px
    color: #5A5D65
    padding-right: 10px
    border-right: 1px solid #E1E3EA
    margin-right: 10px
    
    &:last-child
      padding-right: 0
      border-right: 0
      margin-right: 0

.schedule-filter
  display: grid
  grid-template-columns: 270px 155px 270px 270px
  grid-template-rows: auto
  gap: 30px

  +r(1420)
    grid-template-columns: 1fr 1fr

  +r(767)
    gap: 20px

  +r(568)
    grid-template-columns: 1fr
    
  .button
    padding: 0 10px

.schedule-item
  padding: 2px 1px
  width: 44px
  display: inline-block
  position: relative
  border-radius: 4px
  +tr(.25s)

  +h
    background:#81A5FF!important
    .schedule-star
      border: 1px solid #81A5FF
      border-bottom: 1px solid #F0F1F4
    .schedule-duration
      border: 1px solid #81A5FF
      border-top: 0

  &__link
    position: absolute
    left: 0
    top: 0
    width: 100%
    height: 100% 
    pointer-events: none

  &.is-active
    .schedule-star
      color: #628EFF
    .schedule-duration
      color: #fff
      background: #628EFF

.schedule-star
  width: 42px
  border: 1px solid #F0F1F4
  border-radius: 4px
  font-weight: 400
  font-size: 12px
  line-height: 16px
  color: #878C98
  border-bottom-left-radius: 0
  border-bottom-right-radius: 0
  +tr(.25s)
  
.schedule-duration
  width: 42px
  border: 1px solid #F0F1F4
  border-radius: 4px
  font-weight: 600
  font-size: 12px
  line-height: 19px
  color: #C3C8D5
  text-align: center
  border-top: 0
  border-top-left-radius: 0
  border-top-right-radius: 0
  +tr(.25s)

.schedule-amount
  width: 22px
  height: 43px
  background: #F8F9FA
  border-radius: 2px
  display: flex
  align-items: center
  justify-content: center
  font-weight: 500
  font-size: 12px
  line-height: 100%
  color: #B4BACA
  +r(1879)
    width: 42px
    border-radius: 4px
  &.is-active
    color: #5A5D65

table.footable.schedule-table 
  margin-top: 30px

  &.history-mode
    .schedule-item__link
      pointer-events: all

  thead tr th
    padding: 0
    font-weight: 500
    font-size: 12px
    line-height: 100%
    color: #878C98
    text-align: center
    padding: 5px 2px 4px 2px
    &.weekend
      background: #E0E8FF
  tbody 
    border-top: 1px solid #B4BACA
    td
      padding: 5px 0 4px
      height: auto
      border-bottom: 1px solid #F0F1F4
      text-align: center

      &::before
        display: none
      &:first-child
        padding: 0
        font-weight: 400
        font-size: 12px
        line-height: 100%
        color: #5A5D65
        padding-right: 12px
        text-align: left
      
      &.td-weekend
        padding-left: 0
        padding-right: 0
        .schedule-item
          background: #D8E3FF
          border-top-left-radius: 4px
          border-bottom-left-radius: 4px
          border-top-right-radius: 0px
          border-bottom-right-radius: 0px
          padding: 2px
          margin-left: 1px
          width: 46px
          +r(1879)
            border-radius: 4px!important

    .td-weekend + .td-weekend
      .schedule-item
        border-top-left-radius: 0
        border-bottom-left-radius: 0
        border-top-right-radius: 4px
        border-bottom-right-radius: 4px
        margin-left: 0
        margin-right: 1px

  .footable-details
    span.weekend
      background: #E0E8FF
      display: flex
      align-items: center
      margin-left: -10px
      margin-right: -1px
      padding-left: 10px
      height: 100%
      height: 55px
      margin-top: -1px
      margin-bottom: -1px
    .td-weekend
      background: #E0E8FF

table.footable.schedule-table  tbody .footable-detail-row table.footable-details th:first-child, table.footable.schedule-table  tbody .footable-detail-row table.footable-details td:first-child
  width: 8%

table.footable-details.schedule-table > tbody > tr > th:first-child
  padding-right: 0

table.footable-details.schedule-table tbody td
  text-align: left

.schedule-name
  font-weight: 400
  font-size: 12px
  line-height: 100%
  color: #5A5D65
  display: block!important
  word-break: break-all
  max-width: 73px
  min-width: 60px

.checkbox-styled
