.worker-info
  display: flex
  flex-wrap: wrap
  margin-bottom: 20px
  &__reward
    background: #F8F9FA
    border-radius: 20px
    flex: 0 0 88px
    height: 66px
    padding: 15px
    display: flex
    align-items: center
  &__reward-status
    width: 35px
    height: 35px
    margin-right: 4px

