  &__reward-count
    font-weight: 500
    font-size: 24px
    line-height: 100%
    color: #878C98
  &__body
    flex: 0 0 calc(100% - 88px)
    padding-left: 15px
  &__name
    font-weight: 500
    font-size: 18px
    line-height: 100%
    color: #30393E
    margin-bottom: 5px
  &__post
    margin-bottom: 15px
    font-weight: 500
    font-size: 12px
    line-height: 100%
    color: #B4BACA
  &__text
    margin-bottom: 12px
    p
      font-weight: 400
      font-size: 12px
      line-height: 130%
      color: #5A5D65
      margin: 0 0 8px
      &:last-child
        margin-bottom: 0

.history-mode-btn
  &.is-active
    border-color: #628EFF
    color: #628EFF

.worker-photo
  background: #F8F9FA
  border-radius: 20px
  display: flex
  align-items: center
  padding: 20px 30px
  justify-content: space-between
  margin-bottom: 20px
  +r(568)
    padding: 10px 10px
  &__col
    flex: 0 0 140px
    +r(568)
      flex: 0 0 110px
  &__label
    font-weight: 400
    font-size: 12px
    line-height: 100%
    text-align: center
    color: #878C98
    margin-bottom: 8px
  &__time
    font-weight: 500
    font-size: 18px
    line-height: 100%
    text-align: center
    color: #30393E
    margin-bottom: 8px
  &__late
    position: absolute
    display: flex
    align-items: center
    justify-content: center
    font-weight: 400
    font-size: 12px
    line-height: 100%
    letter-spacing: 0.02em
    color: #F8F9FA
    left: 0
    bottom: 0
    width: 100%
    z-index: 2
    height: 24px
    background: rgba(48, 57, 62, 0.6)
    backdrop-filter: blur(2px)
    border-radius: 0px 0px 20px 20px
    &::before
      color: #FFDE3F
      font-size: 16px
      margin-right: 5px
      margin-top: -1px
.add-img
  &.--worker-photo
    position: relative
    .dropify-wrapper.touch-fallback
      height: 140px!important
      +r(568)
        height: 110px!important
    .dropify-wrapper
      width: 140px
      height: 140px
      +r(568)
        width: 110px
        height: 110px
    .dropify-wrapper 
      +h
        
      .dropify-message span.file-icon
        &::before
          border: 2px solid #FFDE3F

      .dropify-message span.file-icon
        width: 49px
        height: 49px
        margin-top: 0
        font-size: 31px
        line-height: 1
        &::before
          content: '+'
          font-weight: 700
          line-height: 1
          width: 49px
          height: 49px
          display: flex
          align-items: center
          justify-content: center
          border: 2px solid #628EFF
          border-radius: 14px
          padding-bottom: 10px

.worker-alert
  font-weight: 400
  font-size: 14px
  line-height: 100%
  color: #30393E
  &::before
    color: #FF5F72
    font-size: 24px
    margin-right: 6px

.drop-personnel
  &__item
    min-height: 25px
    position: relative
    padding-left: 40px
    padding-right: 15px
    margin-bottom: 10px
    display: flex
    flex-wrap: wrap
    align-items: center
    cursor: pointer
    +h
      &::before
        opacity: 1
    &::after
      width: 25px
      height: 25px
      content: ''
      position: absolute
      left: 0
      top: 50%
      transform: translateY(-50%)
      border: 1.5px solid #E1E3EA
      border-radius: 50%
    &::before
      width: 10px
      height: 10px
      background: #FFDE3F
      content: ''
      position: absolute
      left: 7px
      top: 50%
      transform: translateY(-50%)
      border-radius: 50%
      opacity: 0
      +tr(.25s)
    &.is-selected
      &::before
        opacity: 1
      &::after
        border-color: #FFDE3F
  &__item-post
    padding-left: 22px
    font-weight: 400
    font-size: 14px
    line-height: 17px
    color: #30393E
    position: relative
  &__item-color
    border-radius: 2px
    width: 14px
    height: 14px
    display: block
    position: absolute
    left: 0
    top: 50%
    transform: translateY(-50%)
  .form-control
    border-radius: 0
    border: 0
    border-bottom: 1px solid #E1E3EA

.drop-personnel-box
  margin-top: 15px

.drop-personnel-overflow
  overflow: auto
  max-height: 235px
  scrollbar-color: #C3C8D5 #F0F1F4
  scrollbar-width: thin!important
  -moz-appearance: none !important

  &::-webkit-scrollbar
    width: 4px

  &::-webkit-scrollbar-track
    background-color: #F0F1F4

  &::-webkit-scrollbar-thumb
    background-color: #C3C8D5
.drop-personnel-list
  margin: 0
  padding: 0
  list-style: none


.personnel-btn
  display: inline-block
  border: 1.5px solid #E1E3EA
  border-radius: 6px
  background: #FFFFFF
  color: #C3C8D5
  font-weight: 100
  font-size: 14px
  height: 45px
  padding: 0 45px 0 15px
  overflow: hidden
  line-height: 45px
  text-overflow: ellipsis
  white-space: nowrap
  box-sizing: border-box
  padding-right: 34px
  background-image: url("data:image/svg+xml,%3Csvg width='20' height='20' viewBox='0 0 20 20' xmlns='http://www.w3.org/2000/svg'%3E%3Cpolyline fill='none' stroke='%23A3AED0' stroke-width='1.03' points='16 7 10 13 4 7'%3E%3C/polyline%3E%3C/svg%3E")
