header.page-head.--instructions
  .page-head__top
    display: flex
    align-items: center
    width: 300px
    background: #1D2022
    border-bottom: 1px solid #FFDE3F
    +r(1359)
      width: 200px
    +r(767)
      width: 100%

    a
      display: flex
      justify-content: center
      align-items: center
      width: 70px
      height: 70px
      flex: 0 0 70px
      background: #FFDE3F
      font-size: 24px
      color: #fff
      +tr(0.25s)
      +h
        background: darken(#FFDE3F, 10%)
      +r(1359)
        width: 50px
        height: 50px
        flex: 0 0 50px

    h3
      font-style: normal
      font-weight: 700
      font-size: 16px
      line-height: 19px
      letter-spacing: 0.05em
      text-transform: uppercase
      color: #FFFFFF
      display: flex
      justify-content: center
      align-items: center
      flex-grow: 1
      height: 70px
      margin: 0
      +r(1359)
        height: 50px
      +r(767)
        font-style: normal
        font-weight: 700
        font-size: 14px
        line-height: 17px
        letter-spacing: 0.05em
        text-transform: uppercase
        color: #FFFFFF


aside.page-sidebar.--instructions
  &::before
    display: none
  .sidebar-menu
    &__link
      position: relative
      padding-left: 48px
      padding-right: 20px
      +r(1359)
        padding-left: 34px
        padding-right: 10px
      &::before
        +r(767)
          width: calc( 100% - 20px )
      &::after
        content: ''
        display: block
        width: 6px
        height: 6px
        flex: 0 0 6px
        background: #60646C
        position: absolute
        top: 14px
        left: 33px
        +r(1359)
          left: 20px
      &.is-active
        &::after
          background: #FFDE3F

.page-sidebar
  &__search
    display: block
    position: relative
    margin: 20px 33px
    +r(1359)
      margin: 20px

.sidebar-search
  position: relative
  .form-control
    width: 100%
    height: 40px
    background-color: #1D2022
    background-image: url(../img/svgico/search.svg)
    background-repeat: no-repeat
    background-position: left 13px center
    background-size: 13px
    border: none
    border-radius: 0
    padding-left: 35px
    padding-right: 40px
    color: #fff
  .form-control-clear
    display: flex
    justify-content: center
    align-items: center
    background-image: url(../img/svgico/clear.svg)
    background-position: center
    background-repeat: no-repeat
    position: absolute
    top: 0
    right: 0
    width: 40px
    height: 40px
    flex: 0 0 40px
    cursor: pointer
    +tr(0.25s)
    opacity: 0
    visibility: hidden
    pointer-events: none
    z-index: -1
    &.--active
      opacity: 1
      visibility: visible
      pointer-events: all
      z-index: 2

.instructions-content
  width: 100%
  max-width: 1170px
  margin-top: 35px
  font-style: normal
  font-weight: 400
  font-size: 14px
  line-height: 140%
  letter-spacing: 0.02em
  color: #30393E
  +r(767)
    margin-top: 25px
    font-style: normal
    font-weight: 400
    font-size: 12px
    line-height: 140%
    letter-spacing: 0.02em
    color: #30393E

  * + p
    margin-top: 15px

  h3
    font-style: normal
    font-weight: 500
    font-size: 20px
    line-height: 24px
    color: #30393E
    margin: 0 0 15px 0
    +r(767)
      font-size: 18px
  h6
    font-style: normal
    font-weight: 500
    font-size: 14px
    line-height: 140%
    letter-spacing: 0.02em
    color: #30393E
    +r(767)
      font-size: 12px
  a
    font-style: normal
    font-weight: 500
    font-size: 14px
    line-height: 140%
    letter-spacing: 0.02em
    color: #628EFF
    +r(767)
      font-size: 12px
  * + h3
    margin-top: 30px
  img
    margin: 10px 0
    max-width: 100%

body.instructions
  overflow: hidden

main.page-main.--instructions
  max-height: calc( 100vh - 70px )
  overflow-y: auto
  scrollbar-color: #C3C8D5 #F0F1F4
  scrollbar-width: thin!important
  -moz-appearance: none !important
  &::-webkit-scrollbar
    width: 4px
  &::-webkit-scrollbar-track
    background-color: #F0F1F4
  &::-webkit-scrollbar-thumb
    background-color: #C3C8D5

  +r(767)
    max-height: calc( 100vh - 100px )

.instruction-navigation
  max-width: 1170px
  display: flex
  justify-content: space-between
  align-items: center
  margin-top: 20px
  padding-top: 20px
  border-top: 1px solid #F0F1F4
  +r(767)
    margin-top: 25px
    padding-top: 15px
    padding-bottom: 25px

  a
    display: inline-flex
    align-items: center
    font-style: normal
    font-weight: 400
    font-size: 14px
    line-height: 17px
    letter-spacing: 0.01em
    color: #30393E
    +tr(0.25s)

    +r(767)
      font-size: 10px

    +ico()
      display: flex
      justify-content: center
      align-items: center
      width: 35px
      height: 35px
      flex: 0 0 35px
      background: #F8F9FA
      border: 1px solid transparent
      border-radius: 6px
      color: #30393E

    span
      +r(767)
        max-width: 70px

    &:first-child
      text-align: left
      +ico()
        margin-right: 10px

    &:last-child
      text-align: right
      +ico()
        margin-left: 10px

    +h
      color: #FFDE3F
      +ico()
        border-color: #FFDE3F

header.page-head.--instructions
  .page-head__content
    height: 70px
    +r(1359)
      height: 50px
      padding: 0 30px
    +r(767)
      display: none

main.page-main.--instructions
  flex: auto
  +r(1359)
    padding: 0 30px
  +r(767)
    padding: 0 10px

.page-content.--instructions
  flex-direction: row!important
  +r(767)
    flex-direction: column!important

  .page-sidebar
    padding: 0
    +r(767)
      display: flex
      flex-direction: column

  .page-sidebar__btn
    display: none
    +r(767)
      display: block
    button
      width: 100%
      background: #1D2022
      border: none
      border-radius: 0
      color: #fff
      height: 50px
      display: flex
      align-items: center
      justify-content: space-between

      .ico_angle-down
        color: $yellow

  .page-sidebar__drop
    overflow: visible
    visibility: visible
    position: static
    transform: none
    margin: 0
    opacity: 1
    width: 300px
    +r(1359)
      width: 200px
    +r(767)
      overflow: hidden
      visibility: hidden
      position: absolute
      left: 0
      transform: translateY(50px)
      margin: 0
      opacity: 0
      width: 100%
    &.is-active
      transform: translateY(0)
      opacity: 1
      visibility: visible

  .page-sidebar__menu
    +r(767)
      max-height: calc( 100vh - 140px )
      overflow-y: auto
      scrollbar-color: $yellow #1D2022
      scrollbar-width: thin!important
      -moz-appearance: none !important
      &::-webkit-scrollbar
        width: 2px
      &::-webkit-scrollbar-track
        background-color: #1D2022
      &::-webkit-scrollbar-thumb
        background-color: $yellow
