.payment-services-img
  display: flex
  align-items: center
  width: 116px
  height: 35px
  margin-right: 30px
  +r(991)
    margin-right: 15px
  +r(767)
    margin-right: 0
    margin-bottom: 5px
  img
    max-width: 100%

.payment-services-inner
  table.footable tbody td.text-end
    padding-right: 30px !important

.payment-drop
  .action-drop__btn
    background: #F8F9FA
    color: #B4BACA
    +h
      background: #EFF4FF
      color: #628EFF
  .action-drop__box
    padding: 15px

  .action-drop__head
    padding-bottom: 5px
    margin-bottom: 2px

  .action-drop__item
    padding: 10px 0 11px 0

    &:last-child
      padding-bottom: 1px
  
  .action-drop__item + *
    padding-top: 13px
    margin-top: 0

  .action-drop__link
    line-height: 1