.placment
  display: flex
  flex-wrap: wrap

  +r(991)
    flex-direction: column

.placment-view 
  align-items: flex-start

  +r(991)
    order: 2
  +rmin(992)
    flex: 0 0 calc( 100% - 300px )
  img
    max-width: 100%

.placment-sidebar
  max-width: 300px

  +r(991)
    order: 1
    margin-bottom: 20px

  +rmin(992)
    padding-left: 30px
    flex: 0 0 300px

  .form-control
    margin-bottom: 20px
  
  &__btns
    display: grid
    grid-template-columns: 1fr 45px
    gap: 20px

  .color-button
    padding-left: 48px
    &__preview
      border-radius: 6px
      right: auto
      left: 15px 
      width: 25px
      height: 25px

.button.ico_trash
  background: #F8F9FA
  border-color: #F8F9FA
  color: #B4BACA
  &::before
    margin: 0
    font-size: 30px


.hall-name
  box-sizing: border-box
  display: flex
  align-items: center
  height: 45px
  padding: 0 15px
  overflow: hidden
  border: 1.5px solid #E1E3EA
  border-radius: 6px
  background: #FFFFFF
  color: #000
  font-style: normal
  font-weight: 100
  font-size: 14px
  line-height: 3
  text-overflow: ellipsis
  white-space: nowrap
  width: 100%
  margin-bottom: 20px

  &__color
    width: 14px
    height: 14px
    border-radius: 2px
    margin-right: 8px
    display: block


.hall-btns 
  display: flex
  overflow-y: auto
  margin-bottom: 30px

.hall-btn
  display: flex
  align-items: center
  border: 0
  outline: 0
  margin-right: 10px
  padding: 10px
  +tr(.25s)
  white-space: nowrap

  &:last-child
    margin-right: 0

  &__color
    width: 14px
    height: 14px
    border-radius: 2px
    margin-right: 8px

  &__text
    font-weight: 400
    font-size: 14px
    line-height: 17px
    color: #30393E

  +h
    background: #F0F1F4
  &.is-active
    background: #F0F1F4


.table-previwe
  img
    max-width: 94px

.hall-previwe-btns
  display: grid
  grid-template-rows: repeat(2, 1fr)
  gap: 20px

.add-previwe-btn
  border: 0
  font-weight: 500
  font-size: 14px
  line-height: 100%
  letter-spacing: 0.02em
  color: #30393E
  box-shadow: 8px 8px 50px 3px #F0F1F4
  width: 100%
  position: relative
  border-radius: 20px
  display: flex
  align-items: center
  justify-content: center
  background: #FFFFFF
  border-radius: 20px
  min-height: 100px

  &::before
    content: '+'
    font-weight: 500
    line-height: 1
    width: 35px
    height: 35px
    font-size: 21px
    display: flex
    align-items: center
    justify-content: center
    border: 2px solid #628EFF
    margin-right: 12px
    border-radius: 50%
    color: #628eff
    padding-bottom: 5px
    
.hall-previwe-wrap
  display: grid
  gap: 30px
  grid-template-columns: repeat(4, 1fr)

  +r(1199)
    grid-template-columns: repeat(3, 1fr)

  +r(767)
    gap: 20px

  +r(658)
    grid-template-columns: repeat(2, 1fr)

  +r(458)
    grid-template-columns: repeat(1, 1fr)


.hall-previwe
  background: #F8F9FA
  border-radius: 20px
  padding: 24px
  position: relative
  +tr(.25s)
  min-width: 0

  .action-group-list li button.ico_checkmark:hover, .action-group-list li a.ico_checkmark:hover, .action-group-list li button.ico_checkmark:focus, .action-group-list li a.ico_checkmark:focus
    color: #57CA70!important
    background: #EEFAF1!important

  +h
    background: #EFF4FE

  &__nav 
    position: absolute
    right: 0
    top: 24px
    color: #B4BACA
    width: 24px
    text-align: center
    height: 24px
    display: flex
    align-items: center
    justify-content: center
    cursor: pointer

    +h
      color: #628eff

    .action-group-btn
      background: transparent
      width: 24px
      height: 24px

    .action-group-drop
      top: 0
      right: 9px
      border-top-right-radius: 0
    
  &__picture
    border-radius: 20px
    overflow: hidden
    position: relative
    padding-top: 100%
    margin-bottom: 12px

    +h
      .hall-previwe__picture-hover
        opacity: 1

    img
      width: 100%
      height: 100%
      position: absolute
      left: 0
      top: 0
      object-fit: cover
      object-position: center
      z-index: 1

  &__picture-hover
    border: 0
    position: absolute
    border-radius: 20px
    left: 0
    top: 0
    width: 100%
    height: 100%
    opacity: 0
    display: block
    z-index: 2
    +tr(.25s)
    +icon(full-screen)
    display: flex
    align-items: center
    justify-content: center
    font-size: 24px
    color: #628EFF

    &::after 
      content: ''
      border-radius: 20px
      width: 100%
      height: 100%
      background: #30393E
      display: block
      position: absolute
      left: 0
      top: 0
      opacity: 0.5
      z-index: -1

  &__name
    font-weight: 400
    font-size: 12px
    line-height: 100%
    color: #30393E
    margin-bottom: 5px
    display: -webkit-box
    -webkit-line-clamp: 2
    overflow: hidden
    -webkit-box-orient: vertical

  &__desc
    font-weight: 400
    font-size: 10px
    line-height: 100%
    color: #878C98
    display: -webkit-box
    -webkit-line-clamp: 2
    overflow: hidden
    -webkit-box-orient: vertical

.hall-previwe.--video
  .hall-previwe__bottom
    padding-left: 35px
    position: relative

    &::after
      content: ''
      width: 25px
      height: 25px
      border-radius: 50%
      border: 2px solid #628EFF
      position: absolute
      left: 0
      top: 0

    &::before
      content: ''
      width: 0
      height: 0
      border-style: solid
      border-width: 4.5px 0 4.5px 5px
      border-color: transparent transparent transparent #628EFF
      position: absolute
      left: 11px
      top: 8px

  .hall-previwe__name
    white-space: nowrap
    text-overflow: ellipsis

  .hall-previwe__desc
    -webkit-line-clamp: 3
    word-break: break-all

.hall-previwe-main
  position: relative
  +icon(checkmark)
  &::before
    position: absolute
    left: 34px
    top: 34px
    width: 26px
    height: 26px
    color: #57CA70
    border-radius: 50%
    background: rgba(255, 255, 255, 0.9)
    z-index: 5
    display: flex
    align-items: center
    justify-content: center
    font-size: 20px


.full-photo
  display: flex
  align-items: center
  justify-content: center
  img
    max-width: 100%
    max-height: 800px
    object-fit: cover
    display: block
    +rmin(1024)
      max-height: 600px