.structure-users
  display: block
  position: relative
  padding: 30px
  background: #F8F9FA
  &__title
    display: block
    font-style: normal
    font-weight: 500
    font-size: 20px
    line-height: 1.2
    color: #30393E
    margin-bottom: 20px
  &__subtitle
    display: block
    font-style: normal
    font-weight: 500
    font-size: 16px
    line-height: 19px
    color: #30393E
    margin-bottom: 20px
  &__list
    list-style-type: none
    padding: 0
    margin: 0

.structure-user
  display: flex
  justify-content: space-between
  align-items: center
  padding-bottom: 10px
  margin-bottom: 10px
  border-bottom: 1px solid #F0F1F4
  &:last-child
    padding-bottom: 0
    margin-bottom: 0
    border-bottom: 0
  &__icon
    display: flex
    justify-content: center
    align-items: center
    width: 40px
    height: 40px
    flex: 0 0 40px
    img
      width: 40px
      height: 40px
      flex: 0 0 40px
      object-fit: cover
      object-position: center
  &__info
    flex-grow: 1
    margin: 0 12px
  &__name
    display: block
    font-style: normal
    font-weight: 500
    font-size: 14px
    line-height: 120%
    letter-spacing: 0.01em
    color: #30393E
    margin-bottom: 1px
  &__position
    font-style: normal
    font-weight: 400
    font-size: 12px
    line-height: 120%
    letter-spacing: 0.01em
    color: #30393E
  &__btn
    display: block

.custome-table.--structure-users

  .custome-table__row
    grid-template-columns: auto 95px

  .home
    display: flex
    justify-content: center
    align-items: center
    position: relative
    color: #628EFF
    font-size: 25px
    +icon(home1)

  .custome-table__cell .home
    margin-right: 15px
    margin-left: 30px
    +r(1199)
      margin-left: 15px
      margin-right: 7px

  .custome-table__cell .home:before
    width: 25px
    height: 25px
    flex: 0 0 25px

  .custome-table__dropdown .custome-table__dropdown-title .custome-table__cell-group .custome-table__cell:last-child
    +r(767)
      position: static
      right: auto
      bottom: auto
      display: flex
      flex-direction: row
      justify-content: flex-end

.service-inner-editor
  height: calc( 100% - 30px )
  +rmin(992px)
    height: auto