.password-quality__list li + li
  margin-left: 2px
  

.sub-info
  font-weight: 400
  font-size: 12px
  line-height: 100%
  letter-spacing: 0.01em
  color: #878C98
  display: block!important

.add-img-row
  display: grid
  align-items: start
  gap: 30px
  +rmin(768)
    padding-right: 30px
    grid-template-columns: auto 185px

.title-xsmall
  font-weight: 400
  font-size: 14px
  line-height: 100%
  color: #30393E
  margin: 0
.info-tooltip-cont
  position: relative
  padding-right: 30px
.info-tooltip 
  position: absolute
  right: 0
  top: 0
  height: 20px
  +h
    .info-tooltip__drop
      opacity: 1
      pointer-events: all
      transform: translate(0,0)

  &__icon
    color: #FF5F72
    opacity: 0.5
    font-size: 18px
  
  &__drop
    background: #FFFFFF
    box-shadow: 5px 5px 30px 3px #F0F1F4
    border-radius: 5px
    padding: 10px
    position: absolute
    right: 0
    top: 100%
    width: 200px
    font-size: 12px
    +tr(.25s)
    opacity: 0
    pointer-events: none
    transform: translate(0,50px)