.search-select
  display: block
  position: relative
  &__contry
    font-weight: 400
    font-size: 14px
    line-height: 1
    color: #FFFFFF
  &__code
    font-weight: 400
    font-size: 14px
    line-height: 1
    color: #B4BACA

  .form-control
    padding: 0 45px
    border: 2px solid #FFDE3F
    border-radius: 6px
    background: transparent 
    font-weight: 400
    font-size: 14px
    color: #FFFFFF
    background-image: url('data:image/svg+xml;utf8,<svg width="18" height="18" viewBox="0 0 18 18" fill="none" xmlns="http://www.w3.org/2000/svg"><path fill-rule="evenodd" clip-rule="evenodd" d="M2.05729 8.43864C2.05729 4.91324 4.91519 2.05534 8.44059 2.05534C11.966 2.05534 14.8239 4.91324 14.8239 8.43864C14.8239 11.964 11.966 14.8219 8.44059 14.8219C4.91519 14.8219 2.05729 11.964 2.05729 8.43864ZM8.44059 0.388672C3.99472 0.388672 0.390625 3.99276 0.390625 8.43864C0.390625 12.8845 3.99472 16.4886 8.44059 16.4886C10.3634 16.4886 12.1287 15.8145 13.5131 14.6897L16.188 17.3646C16.5135 17.69 17.0411 17.69 17.3666 17.3646C17.692 17.0392 17.692 16.5115 17.3666 16.1861L14.6916 13.5112C15.8164 12.1267 16.4906 10.3614 16.4906 8.43864C16.4906 3.99276 12.8865 0.388672 8.44059 0.388672Z" fill="%235A5D65"/></svg>')
    background-position: left 14px center
    background-size: 15px
    background-repeat: no-repeat

.search-select-clear
  
.search-select-box
  position: absolute
  width: 100%
  top: calc( 100% + 20px )
  
.search-select-overflow
  overflow: auto
  max-height: 190px
  scrollbar-color: #5A5D65 #1D2022
  scrollbar-width: thin!important
  -moz-appearance: none !important
  padding-right: 13px

  &::-webkit-scrollbar
    width: 4px

  &::-webkit-scrollbar-track
    background-color: #1D2022

  &::-webkit-scrollbar-thumb
    background-color: #5A5D65

.search-select-list
  margin: 0
  padding: 0
  list-style: none

.search-select-item
  height: 45px
  display: flex
  align-items: center
  justify-content: space-between
  padding-left: 45px
  padding-right: 10px
  position: relative
  cursor: pointer
  padding-top: 3px
  background: #25282B
  +tr(.25s)
  +h
    background: #1D2022
    &::before
      opacity: 1
  &::after
    content: ''
    width: 25px
    height: 25px
    position: absolute
    border-radius: 50%
    border: 1.5px solid #5A5D65
    left: 10px
    top: 50%
    transform: translateY(-50%)
    +tr(.25s)
  &::before
    background: #FFDE3F
    content: ''
    width: 10px
    height: 10px
    position: absolute
    border-radius: 50%
    left: 17.5px
    top: 50%
    transform: translateY(-50%)
    opacity: 0
    +tr(.25s)
  &.is-selected 
    &::after
      border: 1.5px solid #FFDE3F
    &::before
      opacity: 1

