.message
  color: #000

  &.--new
    p
      color: #000
  p
    margin-bottom: 0
    margin-top: 0
    color: #878C98

  span
    font-weight: 400
    font-size: 12px
    line-height: 100%
    display: flex
    align-items: center
    letter-spacing: 0.01em
    color: #FF5F72
    margin-bottom: 5px

.messages-box
  padding: 30px
  background: #F8F9FA
  margin-bottom: 30px
  +r(1199)
    padding: 20px
    margin-bottom: 20px
  +r(767)
    padding: 20px 17px
.messages-list-box
  scrollbar-color: #C3C8D5 #F0F1F4
  scrollbar-width: thin!important
  -moz-appearance: none !important
  height: 406px
  overflow-y: auto

  &::-webkit-scrollbar
    width: 4px

  &::-webkit-scrollbar-track
    background-color: #F0F1F4

  &::-webkit-scrollbar-thumb
    background-color: #C3C8D5

.messages-list
  margin: 0
  padding: 0 30px 0 0
  list-style: none
  +r(1199)
    padding-right: 20px

  &__item
    display: flex
    align-items: flex-start
    margin-bottom: 15px
    +r(767)
      flex-direction: column
    &:last-child
      margin-bottom: 0
    &.--user
      .messages-list__item-text
        background: rgba(255, 222, 63, 0.3)
      .messages-list__item-avatar
        background: #FFDE3F
    &.--maneger
      .messages-list__item-text
        background: rgba(98, 142, 255, 0.2)
      .messages-list__item-avatar
        background: #628EFF

  &__item-avatar
    display: flex
    justify-content: center
    align-items: center
    width: 60px
    height: 60px
    flex: 0 0 60px
    margin-right: 15px
    border-radius: 50%
    +r(767)
      margin: 0 0 15px 0
    img, svg
      object-fit: cover
      object-position: center
      max-width: 100%

  &__item-content
    display: flex
    flex-direction: column
  &__item-text
    border-radius: 0px 8px 8px 8px
    padding: 15px
    margin-bottom: 8px
    font-weight: 400
    font-size: 14px
    line-height: 140%
    letter-spacing: 0.02em
    color: #3C3C3B
  &__item-info
    letter-spacing: 0.02em
    color: #C3C8D5
    font-weight: 500
    font-size: 12px
    line-height: 100%

.messages-form-box
  text-align: right
  textarea
    margin-bottom: 30px
    +r(1199)
    margin-bottom: 20px
