.sitemap-table
  &__row
    display: grid
    grid-template-columns: 100%
    border-bottom: 1px solid #F0F1F4
    +rmin(768)
      grid-template-columns: calc( 60% - 90px ) 180px calc( 40% - 90px )
    +rmin(992)
      grid-template-columns: calc( 48% - 90px ) 180px calc( 52% - 90px )

    > *
      padding: 10px 30px
      color: #30393E
      font-size: 14px
      line-height: 120%
      letter-spacing: 0.02em
      position: relative
      +r(767)
        padding: 10px 15px
      .drop
        max-width: 280px

    +rmin(768)
      > *:first-child
        padding: 10px 15px 10px 30px
      > *:not(:first-child)::before
          display: block
          position: absolute
          top: 10px
          left: 0
          width: 1px
          height: calc(100% - 20px)
          background: #F0F1F4
          content: ""

    +r(767)
      margin-bottom: 15px

  &__head
        
    +rmin(768)
      > *::before
        display: none!important

    > *
      padding: 12px 30px
      color: #B4BACA
      font-style: normal
      font-weight: 500
      font-size: 12px
      line-height: 1.17
      letter-spacing: 0.02em
      white-space: nowrap

    +rmin(768)
      > *:first-child
        padding: 10px 15px 10px 30px

    +r(767)
      > *:not(:first-child)
        display: none

  &__mobile-label
    color: #B4BACA
    font-style: normal
    font-weight: 500
    font-size: 12px
    line-height: 1.17
    letter-spacing: 0.02em
    white-space: nowrap
    margin-bottom: 5px
    +rmin(768)
      display: none

  &__overflow 
    overflow-y: auto
    max-height: 350px
    padding-right: 24px

    +rmin(768)
      max-height: 540px

    &::-webkit-scrollbar
      width: 4px
    &::-webkit-scrollbar-track
      background-color: #F0F1F4
    &::-webkit-scrollbar-thumb
      background-color: #C3C8D5

.button-group-large
  +r(767)
    flex-direction: column

    > *
      width: 100%
      margin: 0 0 10px 0