.users-type-action
  display: flex
  justify-content: space-between
  align-items: center
  flex-wrap: wrap

  .drop
    &-input
      height: 35px

.users-table
  max-width: 1170px
  thead
    th
      padding: 10px 15px
      +r(1399)
        padding: 10px
  tbody
    tr
      td
        font-size: 12px
        padding: 10px 15px
        word-break: break-all
        +r(1399)
          padding: 10px
        & > *
          word-break: break-all

    &.tb-sortable
      tr
        &.ui-sortable-helper
          max-width: 1170px
          td:nth-child(2)
            width: 100%
        td:first-child
          color: $accent
          cursor: grab

table.users-table tr th:last-child,
table.users-table tr td:last-child
  min-width: auto

.table-before-btn + table.users-table tr th.footable-last-visible
  padding-right: 65px !important

.user-info
  background: #F8F9FA
  padding: 30px 0 30px 30px
  max-width: 370px

  .scroll-box
    max-height: 190px
    overflow: auto

.user-info-btns
  max-width: 370px

.list-info-title
  display: block
  position: relatives
  margin-bottom: 5px

  font-style: normal
  font-weight: 400
  font-size: 14px
  line-height: 100%
  display: flex
  align-items: center
  letter-spacing: 0.01em
  color: #B4BACA



.id-adress
  cursor: pointer
  &.--locked
    display: flex
    align-items: center
    flex-direction: row-reverse
    color: #FF5F72
    +icon(locked)
    &::before
      display: inline-block
      width: 18px
      height: 18px
      flex: 0 0 18px
      margin-top: -3px
      margin-left: 12px
      font-size: 18px
      color: #FF5F72
  &.--unlocked
    display: flex
    align-items: center
    flex-direction: row-reverse
    color: #57CA70
    +icon(unlocked)
    &::before
      display: inline-block
      width: 18px
      height: 18px
      flex: 0 0 18px
      margin-top: -3px
      margin-left: 12px
      font-size: 18px
      color: #57CA70
      +tr(0.25s)

.table-before-btn.--users
  & > *
    right: 20px
    +r(1199)
      right: 10px

.user-password
  .show-password
    display: flex
    justify-content: center
    align-items: center
    width: 31px
    height: 31px
    flex: 0 0 31px
    background-color: #F8F9FA
    border-radius: 6px
    top: 7px
    right: 7px

.page-users-head
  display: flex
  margin-top: 15px
  margin-bottom: 30px
  align-items: center
  h4
    margin: 0
    margin-right: 15px

.select-rules
  display: inline-block
  border: 1.5px solid #E1E3EA
  border-radius: 6px
  background: #FFFFFF
  color: #C3C8D5
  font-weight: 100
  font-size: 14px
  height: 45px
  padding: 0 45px 0 15px
  overflow: hidden
  line-height: 45px
  text-overflow: ellipsis
  white-space: nowrap
  box-sizing: border-box
  padding-right: 34px
  background-image: url("data:image/svg+xml,%3Csvg width='20' height='20' viewBox='0 0 20 20' xmlns='http://www.w3.org/2000/svg'%3E%3Cpolyline fill='none' stroke='%23A3AED0' stroke-width='1.03' points='16 7 10 13 4 7'%3E%3C/polyline%3E%3C/svg%3E")
  background-position: right 10px center
  background-size: 15px
  background-repeat: no-repeat
  width: 100%
  text-align: left 