.vacancies-count
  color: #628EFF
  margin-right: 15px

.accordion-cv
  .accordion-header
    display: flex
    justify-content: space-between
    align-items: center
    padding-right: 15px
    +r(767)
      padding-right: 1px

  +r(568)
    .action-group-list li
      margin: 0 2px

.response-info
  display: grid
  gap: 20px
  padding: 20px 30px
  background: #F8F9FA
  max-width: 501px
  margin-bottom: 30px

  +rmin(568)
    grid-template-columns: repeat(3, auto)
    gap: 20px

  &__label
    font-weight: 500
    font-size: 12px
    line-height: 100%
    display: flex
    align-items: center
    color: #B4BACA
    margin-bottom: 5px

  &__data
    font-weight: 400
    font-size: 14px
    line-height: 100%
    color: #30393E

.employee-company
  padding: 20px
  background: #F8F9FA
  margin-bottom: 15px

  &__name
    font-weight: 500
    font-size: 14px
    line-height: 100%
    color: #30393E
    margin-top: 0
    margin-bottom: 12px

.response-inner
  .employee-group
    margin-bottom: 12px

  .accordion-body
    padding-left: 45px
  +r(568)
    .button-group
      > *
        width: 100%
        margin: 0 0 15px 0

.fild-list
  margin: 0
  padding: 0
  list-style: none

.employee-post
  font-weight: 500
  font-size: 14px
  line-height: 100%
  color: #30393E

.employee-cv-link
  color: #30393E
  img
    max-width: 49px
    margin-right: 10px
